import { Flex, Text, Spacer, Icon, Center } from "@chakra-ui/react";
import { MdError } from "react-icons/md";

interface CustomerCardProps {
  customerName: string;
  customerID: string;
  industry: string;
  contractNum: number;
  errorMessage?: string;
  onClick?: (value: string) => void;
}

const CustomerCard = (props: CustomerCardProps) => {
  const {
    customerName,
    customerID,
    industry,
    contractNum,
    errorMessage,
    onClick,
  } = props;

  return (
    <Center
      w="267px"
      h="161"
      color="#504E4E"
      flexDirection="column"
      boxShadow="outline"
      style={{
        boxShadow:
          onClick !== undefined
            ? "0px 4px 5px rgba(0, 0, 0, 0.5)"
            : "0px 1px 0px rgba(0, 0, 0, 0.2)",
      }}
      onClick={() => {
        if (onClick !== undefined) {
          onClick(customerID);
        }
      }}
    >
      <Flex
        w="200px"
        h="22px"
        color="#EB5757"
        fontSize="10px"
        direction="row"
        border="1px"
        borderColor="#EB5757"
        borderRadius="3px"
        alignItems="center"
        padding="5px"
        visibility={errorMessage ? "visible" : "hidden"}
      >
        <Icon w="18px" h="18px" as={MdError} />
        <Spacer />
        <Text>{errorMessage}</Text>
        <Spacer />
      </Flex>
      <Text margin="5px" fontSize="18px" fontWeight="bold">
        {customerName}
      </Text>
      <Text marginBottom="3px" fontSize="12px">
        {customerID}
      </Text>
      <Text fontSize="14px">{industry}</Text>
      <Flex alignItems="baseline" direction="row">
        <Text fontSize="24px" fontWeight="bold">
          {contractNum}
        </Text>
        <Text marginLeft="15px" fontSize="14px">
          契約
        </Text>
      </Flex>
    </Center>
  );
};

export default CustomerCard;
