import React from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Box, Text } from '@chakra-ui/react';
import axios from 'axios';

import BatteryCard from "../../components/cards/BatteryCard";
import CompanyCard from "../../components/cards/CompanyCard";
import LongUnitDetailCard from "../../components/cards/LongUnitDetailCard";
import SearchBar from "../../components/SearchBar";
import { reduceEachTrailingCommentRange } from 'typescript';

interface UnitDetailViewPageProps {
    location: {
        state: {
            contractID: string,
            unitID: string
        }
    }
}

const UnitDetailViewPage = (props: UnitDetailViewPageProps) => {
    const { location } = props;
    //const contractID = location.state.contractID;
    const unitID = location.state.unitID;
    
    interface Units {
        unit_id: string;
        //customer_name: string;
        //department_name: string;
        //contract_id: number;
        error:{
            error_code:{
                Int32: number;
                Valid: boolean;
            };
            required_action: string;
            charger_error: number;
            error_message: string;
        };
        //errorcode: number;
        profile: {
            unit_type: string;
            purpose: string;
            location: {
                latitude: number;
                longitude: number;
            }
        };
        status:{
            is_charging: boolean;
            is_working: boolean;
            last_io_time: number;
            max_cell_voltage: number;
            max_temperature: number;
            min_cell_voltage: number;
            min_temperature: number;
            number_of_charges: number;
            output_current: number;
            output_voltage: number;
            soc: number;
            usage_time: number;
            current: number;
            voltage: number;

        }
        time_stamps:{
            registerd_at:number;
            time:number;
        }
        last_io_time: number;
        customer_id:number;
        corporation_name: string;
        //position: string,
        //name: string,
        //mail: string,
        //sphone: string
    }

    interface Customer {
        customer_id: string,
        corporation_name: string
    }

    const defaultUnits:Units[] = [];
    const [units, setUnits]: [Units[], (units: Units[]) => void] = React.useState(defaultUnits);

    //battryで一斉取得
    //->battery_id取得
    React.useEffect(() => {
    axios
        .get<Units[]>("https://jugem.live/api/v1/unit/?unit_id="+unitID, {
            headers: {
                "Content-Type": "application/json"
            },
        })
        .then(response => {
            setUnits(response.data);
        });
    }, []);
    console.log('unit data should be fetched at this point');
    console.log(units)

    //customer cardのため、customerが存在する場合はCompanyDatasに入れる
    let CompanyData: Customer
    let customer_id_str: string
    let CompanyDatas: Customer[]
    CompanyDatas = []

    {units.map((value) => {
        if (value.customer_id!=-1){
            customer_id_str=String(value.customer_id)
            CompanyData = {
                //customer_id: "1",
                customer_id: customer_id_str,
                corporation_name: value.corporation_name,
            };
            CompanyDatas[0]=CompanyData
            console.log(CompanyDatas)
        }
        else{
            console.log("no customer")
        }
    })}

    //customerが存在する場合は契約情報も取ってくる
    //

    const history = useHistory();

    return (
        <Center flexDirection="column">
            <Text
                w="290px"
                fontSize="22px"
                fontWeight="bold"
                margin="10px"
            >
                バッテリー詳細
            </Text>
            <Box>
                <SearchBar
                    onChange={()=>{
                        //TODO:フィルタリングの処理
                    }}
                />
            </Box>
            {units.map((value) => {
                return(
                    <Box>
                        <BatteryCard
                            remainingAmount={value.status.soc}
                            company={value.corporation_name}
                            unitID={value.unit_id}
                            batteryType={value.profile.unit_type}
                            purpose={value.profile.purpose}
                            requiredAction={value.error.required_action}
                        />
                    </Box>
                );
            })}

            {CompanyDatas.map((value) => {
                return(
                    <Box margin="15px">
                        <CompanyCard
                            companyName={value.corporation_name}
                            customerID={value.customer_id}
                            onClick={() => {
                                history.push({
                                    pathname: `/customer-info`,
                                    state: {
                                        customerID: value.customer_id
                                    }
                                });
                            }}
                        />
                    </Box>
                );
            })}
            
            {units.map((value) => {
                return(
                <Box margin="15px">
                    <LongUnitDetailCard
                        //TODO:ダミーのデータのため後で入れ替え
                        unitID={value.unit_id}
                        unitType={value.profile.unit_type}
                        purpose={value.profile.purpose}
                        contractState="test"
                        BMSVersion="62B44Ce9/2019"
                        isPower={value.status.is_working}
                        message={value.error.required_action}
                        longitude={value.profile.location.longitude}
                        latitude={value.profile.location.latitude}
                        isCharging={value.status.is_charging}
                        current={value.status.current}
                        voltage={value.status.voltage}
                        errorCount={value.error.error_code.Int32}
                        soc={value.status.soc}
                        outputCurrent={value.status.output_current}
                        outputVoltage={value.status.output_voltage}
                        //isChargeError={value.error.charger_error}
                        isChargeError = {false}
                        useTime={value.status.usage_time}
                        chargeCount={value.status.number_of_charges}
                        cellMaxVol={value.status.max_cell_voltage}
                        cellMinVol={value.status.min_cell_voltage}
                        maxTemperature={value.status.max_temperature}
                        minTemperature={value.status.min_temperature}
                    />
                </Box>
                );
            })}
        </Center>
    );
}

export default UnitDetailViewPage;