import React, { useEffect, useState } from "react";
import { useForm } from "react-hook-form";
import { useHistory } from "react-router-dom";
import { Center, Checkbox, Text, Input, Flex, Box } from "@chakra-ui/react";
import axios from "axios";
import TextField from "@material-ui/core/TextField";

import AWS from "aws-sdk";
import S3 from "aws-sdk/clients/s3";

import {
  apiURL,
  BatteryOption,
  BatteryOptionJson,
} from "../../modules/interfaces";

import HeaderSubBack from "../../components/HeaderSubBack";
import BackButton from "../../components/buttons/BackButton";
import SubmitButton from "../../components/buttons/SubmitButton";
import AddButton from "../../components/buttons/AddButton";

type FormInputs = {
  isNeedChangeBattery: boolean;
  isSuspendMachineBattery: boolean;
  is3mForklift: boolean;
  isFacility: boolean;
  isNeedWiringWork: boolean;
  note: string;
  request: string;
};

interface DepartmentFieldRegistrationView3PageProps {
  location: {
    state: {
      company: string;
      officeName: string;
      account_id: number;
      department_id: number;
      is_update: boolean;
      fieldView1Data: {
        name: string;
        type: string;
        forkliftType: string;
        model: string;
        serial: string;
        voltage: string;
        capacity: string;
        weight: string;
        depth: string;
        width: string;
        height: string;
        batteryCounter: string;
        batteryReach: string;
      };
      fieldView2Data: {
        dayH: string;
        dayM: string;
        chargeTime: string;
        isChangeBattery: boolean;
        changeBatteryCount: string;
        isForkliftTemp: boolean;
        isForkliftDust: boolean;
        isForkliftElse: boolean;
        forkliftEtc: string;
        //isBatteryTemp: boolean;
        //isBatteryDust: boolean;
        //batteryEtc: string;
        isPureCarPlug: string;
        carPlugEtc: string;
        isPureBatteryPlug: string;
        batteryPlugEtc: string;
      };
    };
  };
}

const DepartmentFieldRegistrationView3Page = (
  props: DepartmentFieldRegistrationView3PageProps
) => {
  const { location } = props;
  const company = location.state.company;
  const officeName = location.state.officeName;
  const account_id = location.state.account_id;
  const department_id = location.state.department_id;
  const is_update = location.state.is_update;
  console.log(props);

  let isNeedChangeBattery = false;
  const [isNeedChangeBatteryValue, setIsNeedChangeBatteryValue] = useState(
    isNeedChangeBattery
  );
  const [imageFile, setImageFile] = useState<File | null>(null);

  AWS.config.update({
    accessKeyId: process.env.AWS_ACCESS_KEY,
    secretAccessKey: process.env.AWS_SECRET_KEY,
  });

  const {
    register,
    formState: { errors },
    handleSubmit,
  } = useForm<FormInputs>();

  const onSubmit = (data: FormInputs) => {
    let json: BatteryOptionJson;
    let environment_state = "";
    if (location.state.fieldView2Data.isForkliftTemp) {
      environment_state = "room";
    }
    if (location.state.fieldView2Data.isForkliftDust) {
      environment_state = "Dust";
    }
    /*
    if (location.state.fieldView2Data.isBatteryTemp) {
      environment_state = "room";
    }
    if (location.state.fieldView2Data.isBatteryDust) {
      environment_state = "Dust";
    }
    */
    /*
    let input_plug_json = "";
    if (location.state.fieldView2Data.isPureBatteryPlug) {
      input_plug_json = "genuine";
    }
    let output_plug_json = "";
    if (location.state.fieldView2Data.isPureCarPlug) {
      output_plug_json = "genuine";
    }
    */

    let isNeedChangeBattery = "";
    if(data.isNeedChangeBattery==false){
      isNeedChangeBattery="noNeed"
    }else if(data.isNeedChangeBattery){
      if(data.isSuspendMachineBattery){
        isNeedChangeBattery="lift"
      }else if(data.is3mForklift){
        isNeedChangeBattery="3Mlift"
      }else{
        isNeedChangeBattery="noEquipment"
      }
    }


    if (imageFile) {
      if (!imageFile.name.match(/^[0-9a-zA-Z\.\_\-]*$/)) {
        console.log("ERROR: ファイル名が正しくありません");

        return;
      }
      // TODO: identityIDの設定
      const accessKeyId = process.env.REACT_APP_AWS_ACCESS_KEY;
      const secretAccessKey = process.env.REACT_APP_AWS_SECRET_KEY; // IAMユーザのシークレットアクセスキー。アクセスキーを作ったときだけ見れるやつです。
      const bucketName = process.env.REACT_APP_BUCKET; // 保存先のバケット名
      const bucket = new S3({
        accessKeyId: accessKeyId,
        secretAccessKey: secretAccessKey,
        region: "ap-northeast-1",
      });
      const param: S3.Types.PutObjectRequest = {
        Bucket: bucketName!,
        Key: "biz/"+imageFile.name, // ファイル絶対パス
        Body: imageFile, // ファイルの内容
        ACL: "public-read", // インターネットから誰でもダウンロードできるように
        ContentType: imageFile.type,
      };
      bucket.upload(param, (err: Error, data: S3.ManagedUpload.SendData) => {
        if (err) {
          console.error(err);
          return;
        } else {
          console.log("Successfully uploaded file.", data);
        }
      });
    }

    json = {
      battery_option_id: String(option_last_id + 1),
      department_id: String(department_id),
      contract_id: "",
      update_date: "",
      info_type: "field",
      option_name: location.state.fieldView1Data.type,
      forklift: location.state.fieldView1Data.forkliftType,
      type: location.state.fieldView1Data.model, //型式
      serial_number: location.state.fieldView1Data.serial,
      voltage: location.state.fieldView1Data.voltage,
      capacity: location.state.fieldView1Data.capacity,
      weight: location.state.fieldView1Data.weight,
      vertical: location.state.fieldView1Data.height,
      horizontal: location.state.fieldView1Data.width,
      height: location.state.fieldView1Data.height,
      how2change: location.state.fieldView1Data.batteryCounter,
      daily_working_minute: location.state.fieldView2Data.dayM,
      daily_charging_minute: location.state.fieldView2Data.chargeTime,
      number_of_change: location.state.fieldView2Data.changeBatteryCount,
      forklift_environment: environment_state,
      environment_else: location.state.fieldView2Data.forkliftEtc,
      input_plug: location.state.fieldView2Data.isPureBatteryPlug,
      output_plug: location.state.fieldView2Data.isPureCarPlug,
      change_help: isNeedChangeBattery,//String(data.isNeedChangeBattery),
      comment: data.note,
      request: data.request,
      pic_forklift: imageFile?.name || "",
      //pic_forklift_plate: number,
      //pic_battery: number,
      //pic_charger_place: number,
      //pic_battery_plug: number,
      //pic_forklift_plug: number,
      //pic_change_equipment: number,
    };
    console.log(json);

    axios
      .post(apiURL + "api/v1/batteryoption/post/", json, {
        headers: {
          "Content-Type": "applicaton/json",
          //"Content-Type": "text/plain",
        },
      })
      .then(function (response) {
        console.log(response.data);
      })
      .catch(function (error) {
        console.log(error);
      })
      .then(function () {
        console.log("*** 終了 ***");
      });

    console.log("add new battery options to DB");

    history.push({
      pathname: `/department-registration`,
      state: {
        company: company,
        officeName: officeName,
        account_id: account_id,
        department_id: department_id,
        is_update: true,
      },
    });
  };

  const history = useHistory();

  //battery_option 最新idを入手
  //battery_optionデータの取得
  const defaultOptions: BatteryOption[] = [];
  const [options, setOptions]: [
    BatteryOption[],
    (options: BatteryOption[]) => void
  ] = React.useState(defaultOptions);

  React.useEffect(() => {
    axios
      .get<BatteryOption[]>(apiURL + "api/v1/batteryoptions/", {
        headers: {
          "Content-Type": "application/json",
        },
      })
      .then((response) => {
        setOptions(response.data);
      });
  }, []);

  console.log(options);

  console.log("battery option's last_id is");
  let option_last_id = 0;
  if (options!=null && options.length != 0) {
    let len = options.length;
    option_last_id = options[len - 1].battery_option_id;
  }
  console.log(option_last_id);

  return (
    <div>
      <HeaderSubBack
        headerText="車体情報の登録 3/3"
        onClick={() => {
          //location.state.fieldView1Data.forkliftType=""
          history.push({
            pathname: "/department-registration2",
            state: {
              company: company,
              officeName: officeName,
              account_id: account_id,
              department_id: department_id,
              is_update: is_update,
              fieldView1Data: location.state.fieldView1Data,
              fieldView2Data: location.state.fieldView2Data,
            },
          });
        }}
      />
      <Center flexDirection="column" padding="15px">
        <form onSubmit={handleSubmit(onSubmit)}>
          <Box margin="20px" marginTop="20px" marginLeft="40px">
            <Text w="260px" fontSize="18px" fontWeight="bold">
              車体情報を入力してください
            </Text>
          </Box>
          <Box margin="20px" marginLeft="40px">
            <Text
              w="260px"
              fontSize="11px"
              fontWeight="bold"
              color="#504E4E"
              marginBottom="10px"
            >
              バッテリーの交換作業
            </Text>
            <Checkbox
              w="260px"
              marginBottom="15px"
              {...register("isNeedChangeBattery")}
              isInvalid={errors.isNeedChangeBattery ? true : false}
              checked={isNeedChangeBatteryValue}
              onChange={(e) => {
                setIsNeedChangeBatteryValue(e.target.checked);
              }}
            >
              <Text fontSize="11px" fontWeight="bold">
                必要
              </Text>
            </Checkbox>
            {isNeedChangeBatteryValue && (
              <Flex w="260px" direction="column" marginBottom="15px">
                <Text
                  w="260px"
                  fontSize="11px"
                  fontWeight="bold"
                  color="#504E4E"
                  marginBottom="10px"
                >
                  必要の場合
                </Text>
                <Checkbox
                  marginLeft="20px"
                  marginBottom="10px"
                  {...register("isSuspendMachineBattery")}
                >
                  <Text fontSize="11px" fontWeight="bold">
                    吊り下げ装置あり
                  </Text>
                </Checkbox>
                <Checkbox
                  marginLeft="20px"
                  marginBottom="10px"
                  {...register("is3mForklift")}
                >
                  <Text fontSize="11px" fontWeight="bold">
                    3M以上マストのフォークリフトあり
                  </Text>
                </Checkbox>
                <Checkbox marginLeft="20px" {...register("isFacility")}>
                  <Text fontSize="11px" fontWeight="bold">
                    設備なし
                  </Text>
                </Checkbox>
              </Flex>
            )}
            <Text
              w="260px"
              fontSize="11px"
              fontWeight="bold"
              color="#504E4E"
              marginBottom="10px"
            >
              お客様特有の症状など、特記事項
            </Text>
            {/*
        <Input
          w="260px"
          marginBottom="20px"
          placeholder="特になし"
          {...register("note")}
        />
        */}
            <Flex w="260px" direction="row" marginBottom="40px">
              {/*<Input w="269px" h="28px" {...register("comment")} />*/}
              <TextField
                //id="outlined-multiline-static"
                //label="Multiline"
                multiline
                rows={4}
                defaultValue=""
                //variant="outlined"
                {...register("note")}
              />
            </Flex>
            <Text
              w="260px"
              fontSize="11px"
              fontWeight="bold"
              color="#504E4E"
              marginBottom="10px"
            >
              要望・課題
            </Text>
            <Flex w="260px" direction="row" marginBottom="40px">
              {/*<Input w="269px" h="28px" {...register("comment")} />*/}
              <TextField
                //id="outlined-multiline-static"
                //label="Multiline"
                multiline
                rows={4}
                defaultValue=""
                //variant="outlined"
                {...register("request")}
              />
            </Flex>
            {/*
        <Input
          w="260px"
          marginBottom="20px"
          placeholder="特になし"
          {...register("request")}
        />
        */}
            <Box marginLeft="20px" marginBottom="50px">
              <Input
                type="file"
                onChange={(e) => {
                  if (e.target.files) {
                    setImageFile(e.target.files[0]);
                  }
                }}
              />
            </Box>
          </Box>
          <Flex
            w="260px"
            direction="row"
            justifyContent="space-between"
            margin="40px"
          >
            {/*
          <BackButton
            onClick={() => {
              history.push({
                pathname: `/department-registration2`,
                state: {
                  company: company,
                  officeName: officeName,
                  account_id: account_id,
                  department_id: department_id,
                  fieldView1Data: location.state.fieldView1Data,
                  fieldView2Data: location.state.fieldView2Data,
                },
              });
            }}
          />
          */}
            <SubmitButton type="submit" />
          </Flex>
        </form>
      </Center>
    </div>
  );
};

export default DepartmentFieldRegistrationView3Page;
