import React from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Box, Flex, Text } from '@chakra-ui/react';
import axios from "axios"


import {apiURL,DepartmentFormInputs,Departments,DepartmentsJson} from "../../modules/interfaces"
import BackButton from "../../components/buttons/BackButton";
import SubmitButton from "../../components/buttons/SubmitButton";
import HeaderSubBack from 'components/HeaderSubBack';

interface CustomerDepartmentRegistrationReviewPageProps {
    location: {
        state: DepartmentFormInputs;
    }
}

const CustomerDepartmentRegistrationReviewPage = (props: CustomerDepartmentRegistrationReviewPageProps) => {
    const { location } = props;
    const company = location.state.company;
    const account_id = location.state.account_id
    //console.log("props account_id is:"+account_id)

    const officeName = location.state.officeName;
    const uptime = location.state.uptime;
    const weeklyHoliday = location.state.weeklyHoliday;
    const chargeName = location.state.chargeName;
    const position = location.state.position;
    const mail = location.state.mail;
    const tell = location.state.tell;
    const prefecture = location.state.prefecture;
    const postal = location.state.postal;
    const address = location.state.address;
    const building = location.state.building;

    const history = useHistory();

    const defaultDepartments:Departments[] = [];
    const [departments, setDepartments]: [Departments[], (departments: Departments[]) => void] = React.useState(defaultDepartments);

    React.useEffect(() => {
        axios
            .get<Departments[]>(apiURL+"api/v1/departments/", {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setDepartments(response.data);
            });
        }, []);
        console.log('departments data should be fetched at this point');
        console.log(departments)
    
    let addDepartmentData:DepartmentsJson;
    
    let len=0
    let department_id = 0

    if (departments!=null){
        len = departments.length
    }

    if (len!=0){
        let last_id = departments[len-1].department_id
        department_id = last_id + 1
    }else{
        department_id = 1
    }

    addDepartmentData={
        department_id: String(department_id),
        department_name: officeName,
        parent_id: String(account_id),
        name: chargeName,
        position: position,
        postal_code: postal,
        address: prefecture + address + building,
        mail: mail,
        phone: tell,
        daily_working_hour: uptime,
        weekly_holiday: weeklyHoliday,
    }
    console.log(addDepartmentData)

    return (
        <div>
        <HeaderSubBack
          headerText="登録内容の確認"
          onClick={() => {
            let inputs:DepartmentFormInputs
            inputs={
              company: company,
              account_id: account_id,
              officeName: officeName,
              uptime: uptime,
              weeklyHoliday: weeklyHoliday,
              chargeName: chargeName,
              position: position,
              mail: mail,
              tell: tell,
              prefecture: prefecture,
              postal: postal,
              address: address,
              building: building,
            }
            history.push({
              pathname: "/department-view",
              state: inputs,
            });
          }}
        />
        <Center flexDirection="column">
            <Box w="269px" margin="15px">
                <Text fontSize="22px" fontWeight="bold">
                    新事業所登録
                </Text>
            </Box>
            <Box w="269px" marginBottom="15px">
                <Text fontSize="16px" fontWeight="bold">
                    企業名
                </Text>
                <Text fontSize="16px" fontWeight="bold">
                    {company}
                </Text>
            </Box>
            <Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">事業所名</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {officeName}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">1日の稼働時間</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {uptime}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">週休</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {weeklyHoliday}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">担当者名</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {chargeName}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">役職</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {position}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">メールアドレス</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {mail}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">電話番号</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {tell}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">郵便番号</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {postal}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">都道府県</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {prefecture}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">住所</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {address}
                    </Text>
                </Box>
                <Box marginBottom="15px">
                    <Text fontSize="11px" fontWeight="bold">建物名</Text>
                    <Text
                        w="269px"
                        h="28px"
                        fontSize="11px"
                        fontWeight="bold"
                        bgColor="#C4C4C4"
                    >
                        {building}
                    </Text>
                </Box>
            </Box>
            <Flex
                w="275px"
                marginTop="15px"
                direction="row"
                justifyContent="space-between">
                {/*
                <BackButton
                    onClick={()=>{
                        history.push({
                            pathname: '/department-view',
                            state: {
                                company: company,
                                account_id: account_id,
                                officeName: officeName,
                                uptime: uptime,
                                weeklyHoliday: weeklyHoliday,
                                chargeName: chargeName,
                                position: position,
                                mail: mail,
                                tell: tell,
                                postal: postal,
                                address: address,
                            }
                        });
                    }}
                />
                */}
                <SubmitButton
                    onClick={()=>{
                        console.log("json data")
                        console.log(addDepartmentData)

                        axios
                            .post(apiURL+"api/v1/department/post/", addDepartmentData,
                            {
                                headers: {
                                    "Content-Type": "applicaton/json",
                                    //"Content-Type": "text/plain",
                                },
                            }
                            )
                            .then(function (response) {
                                console.log(response.data)
                                })
                            .catch(function (error) {
                                console.log(error)
                                })
                            .then(function () {
                                console.log ("*** 終了 ***")
                                })
  
                            console.log('add new department to DB');

                        //TODO:事業所情報をDBに保存
                        history.push({
                            pathname: `/department-complete`,

                            state: {
                                company: company,
                                officeName: officeName,
                                account_id: account_id,
                                department_id: department_id,
                            }
                        });
                    }}
                />
            </Flex>
        </Center>
        </div>
    );
}

export default CustomerDepartmentRegistrationReviewPage;