import React from "react";
import {
    Flex,
    Text,
    Center,
    Image,
    Box
} from "@chakra-ui/react";

interface ManuBatteryCardProps {
    batteryID: string,
    batteryType: string,
    forkliftType: string,
    carSerialNum: string,
    batterySupplyVolt: number,
    onClick?: (value: string) => void;
}

const ManuBatteryCard = (props: ManuBatteryCardProps) => {
    const {batteryID,batteryType, forkliftType, carSerialNum, batterySupplyVolt, onClick} = props;

    return (
        <Center
            w="267px"
            padding="10px"
            flexDirection="row"
            boxShadow="outline"
            style={{boxShadow: onClick !== undefined ?
                "2px 2px 4px rgba(0, 0, 0, 0.2)"
                : "0px 1px 0px rgba(0, 0, 0, 0.2)"
            }}
            onClick={() => {
                if (onClick !== undefined) {
                    onClick(batteryID)
                }
            }}>
            
            <Flex direction="column">
                <Box>
                    <Text fontSize="5px">製造番号</Text>
                </Box>
                <Box px={4} marginBottom="10px">
                    <Text fontSize="15px" fontWeight="bold">{props.batteryID}</Text>
                </Box>
                <Box>
                    <Text fontSize="5px">バッテリー型番</Text>
                </Box>
                <Box px={4} marginBottom="10px">
                    <Text fontSize="15px" fontWeight="bold">{props.batteryType}</Text>
                </Box>
                <Box>
                    <Text fontSize="5px">フォークリフト</Text>
                </Box>
                <Box px={4} marginBottom="10px">
                    <Text fontSize="15px" fontWeight="bold">{props.forkliftType}</Text>
                </Box>
                <Box>
                    <Text fontSize="5px">バッテリー電圧</Text>
                </Box>
                <Box px={4} marginBottom="10px">
                    <Text fontSize="15px" fontWeight="bold">{props.batterySupplyVolt}</Text>
                </Box>
            </Flex>
        </Center>
    )
}

export default ManuBatteryCard;