import React from 'react';
import { useHistory } from 'react-router-dom';
import {Center, Box, Text, Divider, Flex} from "@chakra-ui/react";
import axios from 'axios';

import {apiURL,Departments,DepartmentsJson} from "../../modules/interfaces"

import SearchBar from "../../components/SearchBar"
import AddButton from "../../components/buttons/AddButton";
import BackButton from "../../components/buttons/BackButton";
import CompanyCard from "../../components/cards/CompanyCard";
import OfficeCard from "../../components/cards/OfficeCard";
import HeaderSubBack from "../../components/HeaderSubBack";

interface CustomerDepartmentViewPageProps {
    location: {
        state: {
            account_id: number,
            company_name: string,
        }
    }
}

const CustomerDepartmentViewPage = (props: CustomerDepartmentViewPageProps) => {
    const { location } = props;
    const account_id = location.state.account_id;
    const company_name = location.state.company_name;

    const history = useHistory();
    console.log("account_id is " + String(account_id))

    //departmentを取得

    const defaultDepartments:Departments[] = [];
    const [departments, setDepartments]: [Departments[], (departments: Departments[]) => void] = React.useState(defaultDepartments);

    React.useEffect(() => {
    axios
        .get<Departments[]>(apiURL+"api/v1/customerdepartment/?parent_id="+String(account_id), {
            headers: {
                "Content-Type": "application/json"
            },
        })
        .then(response => {
            setDepartments(response.data);
        });
    }, []);
    console.log('departments data should be fetched at this point');
    console.log(departments)

    let department_datas:Departments[] = []
    if (departments==null){
        department_datas=[]
    }else{
        department_datas=departments
    }

    return (
        <div>
        <HeaderSubBack
          headerText="顧客事業所"
          onClick={() => {
            history.push({
              pathname: "/contract-home",
            });
          }}
        />
        <Center padding="20px" flexDirection="column">
            {/*
            <Box>
                <Text fontSize="22px" fontWeight="bold">顧客事業所</Text>
            </Box>
            */}
            <Flex direction="row" marginBottom="5px">
                <Text
                    w="80px"
                    fontSize="18px"
                    fontWeight="bold"
                    color="#504E4E"
                >
                    企業名
                </Text>
                <Text
                    w="150px"
                    fontSize="18px"
                    fontWeight="bold"
                >
                    {company_name}
                </Text>
            </Flex>
            <Box>
                <Text fontSize="11px" margin="15px">{`新たに事務所を登録する場合は以下のボタンより登録を行なってください`}</Text>
            </Box>
            <Divider/>
            <Box margin="15px">
                <AddButton
                    label="新規事務所を登録"
                    onClick={()=>{
                    history.push
                        ({
                            pathname: `/department-view`,
                            state: {
                                account_id: account_id,
                                company: company_name,
                            },
                        })
                    }}
                />
            </Box>
            <Box>
                <Text fontSize="11px" margin="15px">{`現場情報の登録を行なう場合は、以下から事業所を選択してください。`}</Text>
            </Box>
            <Divider/>
            {department_datas.map((value) => {
                return (
                    <Box marginTop="20px">
                        <OfficeCard
                            officeName={value.department_name}
                            onClick={()=>{
                                history.push
                                    ({
                                        pathname: `/department-registration`,
                                        state: {
                                            company: company_name,
                                            officeName: value.department_name,
                                            account_id: account_id,
                                            department_id: value.department_id,
                                        },
                                    })
                            }}
                        />
                    </Box>
                );
            })}
            {/*
            <Box margin="15px">
                <BackButton
                    onClick= {() => {
                        history.goBack();
                    }}
                />
            </Box>
                */}
        </Center>
        </div>
    );
}

export default CustomerDepartmentViewPage;