import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { useHistory } from "react-router-dom";
import {
  Center,
  Box,
  Button,
  Text,
  Input,
  Flex,
  Select,
} from "@chakra-ui/react";
import {
  emailValidation,
  telValidation,
  postalValidation,
  inputValidation,
} from "../../utils/validations";
import {DepartmentFormInputs} from "../../modules/interfaces"
import { prefectures, uptimes, weeklyHolidays } from "../../utils/selection";

import axios from "axios";
import HeaderSub from 'components/HeaderSub';

/*
type FormInputs = {
  officeName: string;
  uptime: string;
  weeklyHoliday: string;
  chargeName: string;
  position: string;
  mail: string;
  tell: string;
  postal: string;
  prefecture: string;
  address: string;
  building: string;
};
*/

interface CustomerDepartmentRegistrationViewPageProps {
  location: { 
    state: DepartmentFormInputs;
  };
}

const CustomerDepartmentRegistrationViewPage = (
  props: CustomerDepartmentRegistrationViewPageProps
) => {
  //形象により決まって、DBに載せるもの
  console.log(props)
  const { location } = props;
  const company = location.state.company; //company name->いらない？
  const account_id = location.state.account_id; //=parent_id
  //console.log("props account_id is:"+account_id)
  let officeName = "";
  let uptime ="";
  let weeklyHoliday="";
  let chargeName="";
  let position = "";
  let mail = "";
  let tell = "";
  let prefecture = "";
  let address = "";
  let postal = "";
  let building = "";
  if (location?.state) {
    officeName = location.state.officeName;
    uptime = location.state.uptime;
    weeklyHoliday = location.state.weeklyHoliday;
    chargeName= location.state.chargeName;;
    position = location.state.position;
    mail = location.state.mail;
    tell = location.state.tell;
    prefecture = location.state.prefecture;
    address = location.state.address;
    postal = location.state.postal;
    building = location.state.building;

    if(address!=null && address!==""){
    if(address.indexOf("県") !== -1){
      prefecture = address.slice(0, address.indexOf("県") + 1);
      address = address.slice(address.indexOf("県") + 1);
    } else if (address.indexOf("県") !== -1) {
      prefecture = address.slice(0, address.indexOf("府") + 1);
      address = address.slice(address.indexOf("府") + 1);
    } else if (address.indexOf("都") !== -1) {
      prefecture = address.slice(0, address.indexOf("都") + 1);
      address = address.slice(address.indexOf("都") + 1);
    } else if (address.indexOf("道") !== -1) {
      prefecture = address.slice(0, address.indexOf("道") + 1);
      address = address.slice(address.indexOf("道") + 1);
    }
  }
  }

  const [addressValue, setAddressValue] = useState(address);
  const [prefectureValue, setPrefectureValue] = useState(prefecture);
  const [uptimeValue, setUptimeValue] = useState(uptime);
  const [weeklyHolidayValue, setWeeklyHolidayValue] = useState(weeklyHoliday);

  const {
    register,
    formState: { errors },
    handleSubmit,
  } = useForm<DepartmentFormInputs>({
    defaultValues: {
      officeName: officeName,
      uptime: uptime,
      weeklyHoliday: weeklyHoliday,
      chargeName: chargeName,
      position: position,
      mail: mail,
      tell: tell,
      prefecture: prefecture,
      address: address,
      postal: postal,
      building: building,
    },
    mode: "onSubmit",
  });

  const history = useHistory();

  const onSubmit = (data: DepartmentFormInputs) => {
    history.push({
      pathname: "/department-review",
      state: {
        company: company, //customer/company_name 表示用
        account_id: account_id, //DBから現状のdepartmentを取ってくるため→department_idを次ページで決定
        officeName: data.officeName, //department_name
        uptime: data.uptime, //daily_working_hour
        weeklyHoliday: data.weeklyHoliday,
        chargeName: data.chargeName, //name
        position: data.position,
        mail: data.mail,
        tell: data.tell,
        postal: data.postal,
        prefecture: data.prefecture,
        address: data.address,
        building: data.building,
      },
    });
  };

  return (
    <div>
        <HeaderSub
          headerText="新規事業所の登録"
        />
    <Center flexDirection="column">
      <form onSubmit={handleSubmit(onSubmit)}>
        <Box w="269px">
          <Text fontSize="18px" fontWeight="bold"　 marginTop="15px" marginBottom="15px">
            事業所情報を入力してください
          </Text>
        </Box>
        <Box w="269px" marginBottom="15px">
          <Text fontSize="16px" fontWeight="bold">
            企業名
          </Text>
          <Text fontSize="16px" fontWeight="bold">
            {company}
          </Text>
        </Box>
        <Box>
          <Box marginBottom="15px">
            <Flex direction="row">
              <Text fontSize="11px" fontWeight="bold">
                事業所名
              </Text>
              {errors.officeName && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Input
              w="269px"
              h="28px"
              isInvalid={errors.officeName ? true : false}
              {...register("officeName", { required: true,
                pattern: {
                  value: inputValidation,
                  message: "スペースのみは入力できません",
                },
              })}
            />
          </Box>
          <Box marginBottom="15px">
            <Flex direction="row">
              <Text fontSize="11px" fontWeight="bold">
                1日の稼働時間
              </Text>
              {errors.uptime && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Select
              w="269px"
              h="28px"
              {...register("uptime", { required: true })}
              isInvalid={errors.uptime ? true : false}
              value={uptimeValue}
              onChange={(e) => {
                setUptimeValue(e.target.value);
              }}
              placeholder="選択してください"
            >
              {uptimes.map((uptime) => {
                return (
                  <option value={`${uptime}`}>{`${uptime}`}</option>
                );
              })}
            </Select>
          </Box>
          <Box marginBottom="15px">
            <Flex direction="row">
              <Text fontSize="11px" fontWeight="bold">
                週休
              </Text>
              {errors.weeklyHoliday && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Select
              w="269px"
              h="28px"
              {...register("weeklyHoliday", { required: true })}
              isInvalid={errors.weeklyHoliday ? true : false}
              value={weeklyHolidayValue}
              onChange={(e) => {
                setWeeklyHolidayValue(e.target.value);
              }}
              placeholder="選択してください"
            >
              {weeklyHolidays.map((weeklyHoliday) => {
                return (
                  <option value={`${weeklyHoliday}`}>{`${weeklyHoliday}`}</option>
                );
              })}
            </Select>
          </Box>
          <Box marginBottom="15px">
            <Flex direction="row">
              <Text fontSize="11px" fontWeight="bold">
                担当者名
              </Text>
              {errors.chargeName && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Input
              w="269px"
              h="28px"
              isInvalid={errors.chargeName ? true : false}
              {...register("chargeName", { required: true,
                pattern: {
                  value: inputValidation,
                  message: "スペースのみは入力できません",
                },
              })}
            />
          </Box>
          <Box marginBottom="15px">
            <Text fontSize="11px" fontWeight="bold">
              役職
            </Text>
            <Input
              w="269px"
              h="28px"
              isInvalid={errors.position ? true : false}
              {...register("position")}
            />
          </Box>
          <Box marginBottom="15px">
            <Flex>
              <Text fontSize="11px" fontWeight="bold">
                メールアドレス
              </Text>
              {errors.mail && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Input
              w="269px"
              h="28px"
              {...register("mail", {
                required: true,
                pattern: {
                  value: emailValidation,
                  message: "メールアドレスの形式で入力してください",
                },
              })}
              isInvalid={errors.mail ? true : false}
            />
            {errors.mail && (
              <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                {errors.mail.message}
              </Text>
            )}
          </Box>
          <Box marginBottom="15px">
            <Flex direction="row">
              <Text fontSize="11px" fontWeight="bold">
                電話番号（ハイフンなし）
              </Text>
              {errors.tell && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Input
              w="269px"
              h="28px"
              {...register("tell", {
                required: true,
                pattern: {
                  value: telValidation,
                  message: "電話番号は半角数字でハイフンなしで入力してください",
                },
              })}
              isInvalid={errors.tell ? true : false}
            />
            {errors.tell && (
              <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                {errors.tell.message}
              </Text>
            )}
          </Box>
          <Box marginBottom="15px">
            <Flex direction="row">
              <Text fontSize="11px" fontWeight="bold">
                郵便番号（ハイフンなし）
              </Text>
              {errors.postal && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Input
              w="269px"
              h="28px"
              {...register("postal", {
                required: true,
                pattern: {
                  value: postalValidation,
                  message: "ハイフンなしで入力してください",
                },
              })}
              isInvalid={errors.postal ? true : false}
              onChange={(e) => {
                const word = e.target.value;
                if (word.match(postalValidation)) {
                  axios
                    .get(
                      `https://madefor.github.io/postal-code-api/api/v1/${word.slice(
                        0,
                        3
                      )}/${word.slice(3, 7)}.json`
                    )
                    .then((response) => {
                      const data = response.data.data[0].ja;
                      setPrefectureValue(data.prefecture);
                      setAddressValue(
                        `${data.address1}${data.address2}${data.address3}${data.address4}`
                      );
                    });
                }
              }}
            />
          </Box>
          <Box marginBottom="15px">
            <Flex direction="row">
              <Text fontSize="11px" fontWeight="bold">
                都道府県
              </Text>
              {errors.prefecture && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Select
              w="269px"
              h="28px"
              {...register("prefecture", { required: true })}
              isInvalid={errors.prefecture ? true : false}
              value={prefectureValue}
              onChange={(e) => {
                setPrefectureValue(e.target.value);
              }}
              placeholder="選択してください"
            >
              {prefectures.map((prefecture) => {
                return (
                  <option value={`${prefecture}`}>{`${prefecture}`}</option>
                );
              })}
            </Select>
          </Box>
          <Box marginBottom="15px">
            <Flex direction="row">
              <Text fontSize="11px" fontWeight="bold">
                住所
              </Text>
              {errors.address && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Input
              w="269px"
              h="28px"
              {...register("address", { required: true,
                pattern: {
                  value: inputValidation,
                  message: "スペースのみは入力できません",
                },
              })}
              isInvalid={errors.address ? true : false}
              value={addressValue}
              onChange={(e) => {
                setAddressValue(e.target.value);
              }}
            />
          </Box>
          <Box marginBottom="15px">
            <Text fontSize="11px" fontWeight="bold">
              建物名
            </Text>
            <Input w="269px" h="28px" {...register("building")} />
          </Box>
        </Box>
        <Button
          w="260px"
          h="40px"
          marginTop="20px"
          color="#504E4E"
          bgColor="white"
          border="2px"
          borderColor="#504E4E"
          type="submit"
        >
          登録内容の確認
        </Button>
      </form>
    </Center>
    </div>
  );
};

export default CustomerDepartmentRegistrationViewPage;
