import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { useHistory } from "react-router-dom";
import {
  Center,
  Box,
  Button,
  Text,
  Input,
  Flex,
  Select,
} from "@chakra-ui/react";
import {
  emailValidation,
  telValidation,
  postalValidation,
  inputValidation,
} from "../../utils/validations";
import {CustomerFormInputs} from "../../modules/interfaces"
import { prefectures, jobtypes } from "../../utils/selection";

import axios from "axios";
import HeaderSubBack from 'components/HeaderSubBack';

/*
type FormInputs = {
  company: string;
  jobType: string;
  name: string;
  position: string;
  mail: string;
  tell: string;
  prefecture: string;
  address: string;
  postal_code: string;
  building: string;
};
*/

interface CustomerRegistrationViewPageProps {
  location?: {
    state: CustomerFormInputs;
  };
}

const CustomerRegistrationViewPage = (
  props: CustomerRegistrationViewPageProps
) => {
  let { location } = props;
  let company = "";
  let jobType = "";
  let name = "";
  let position = "";
  let mail = "";
  let tell = "";
  let prefecture = "";
  let address = "";
  let postal = "";
  let building = "";
  if (location?.state) {
    company = location.state.company;
    jobType = location.state.jobType;
    name = location.state.name;
    position = location.state.position;
    mail = location.state.mail;
    tell = location.state.tell;
    prefecture = location.state.prefecture;
    address = location.state.address;
    postal = location.state.postal;
    building = location.state.building;

    if(address!=null && address!==""){
    if (address.indexOf("県") !== -1) {
      prefecture = address.slice(0, address.indexOf("県") + 1);
      address = address.slice(address.indexOf("県") + 1);
    } else if (address.indexOf("県") !== -1) {
      prefecture = address.slice(0, address.indexOf("府") + 1);
      address = address.slice(address.indexOf("府") + 1);
    } else if (address.indexOf("都") !== -1) {
      prefecture = address.slice(0, address.indexOf("都") + 1);
      address = address.slice(address.indexOf("都") + 1);
    } else if (address.indexOf("道") !== -1) {
      prefecture = address.slice(0, address.indexOf("道") + 1);
      address = address.slice(address.indexOf("道") + 1);
    }
    }
  }
  const [addressValue, setAddressValue] = useState(address);
  const [jobTypeValue, setJobTypeValue] = useState(jobType);
  const [prefectureValue, setPrefectureValue] = useState(prefecture);

  const history = useHistory();

  const {
    register,
    formState: { errors },
    handleSubmit,
  } = useForm<CustomerFormInputs>({
    defaultValues: {
      company: company,
      jobType: jobType,
      name: name,
      position: position,
      mail: mail,
      tell: tell,
      prefecture: prefecture,
      address: address,
      postal: postal,
      building: building,
    },
    mode: "onSubmit",
  });

  const onSubmit = (data: CustomerFormInputs) => {
    history.push({
      pathname: "/registration-review",
      state: {
        company: data.company, //corporation _name
        jobType: data.jobType, //sector
        name: data.name, //name
        position: data.position, //postion
        mail: data.mail, //mail
        tell: data.tell, //phone
        prefecture: data.prefecture,
        address: data.address, //address
        postal: data.postal, //postal_code
        building: data.building,
      },
    });
  };

  return (
    <div>
        <HeaderSubBack
          headerText="新規顧客の登録"
          onClick={() => {
            history.push({
              pathname: "/contract-home",
            });
          }}
        />
    <Center flexDirection="column" padding="15px">
      <form onSubmit={handleSubmit(onSubmit)}>
        <Box>
          <Text fontSize="18px" fontWeight="bold" marginTop="15px" marginBottom="15px">
            顧客情報を入力してください
          </Text>
        </Box>
        <Box>
          <Box marginBottom="15px">
            <Flex direction="row">
              <Text fontSize="11px" fontWeight="bold">
                企業名
              </Text>
              {errors.company && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Input
              w="269px"
              h="28px"
              isInvalid={errors.company ? true : false}
              {...register("company", { 
                required: true,
                pattern: {
                  value: inputValidation,
                  message: "スペースのみは入力できません",
                },
               })}
            />
          </Box>
          <Box marginBottom="15px">
            <Flex direction="row">
              <Text fontSize="11px" fontWeight="bold">
                業種
              </Text>
              {errors.jobType && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Select
              w="269px"
              h="28px"
              {...register("jobType", { required: true })}
              isInvalid={errors.jobType ? true : false}
              value={jobTypeValue}
              onChange={(e) => {
                setJobTypeValue(e.target.value);
              }}
              placeholder="選択してください"
            >
              {jobtypes.map((jobType) => {
                return (
                  <option value={`${jobType}`}>{`${jobType}`}</option>
                );
              })}
            </Select>
            {/*
            <Input
              w="269px"
              h="28px"
              isInvalid={errors.jobType ? true : false}
              {...register("jobType", { required: true })}
            />
            */}
          </Box>
          <Box marginBottom="15px">
            <Flex direction="row">
              <Text fontSize="11px" fontWeight="bold">
                担当者名
              </Text>
              {errors.name && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Input
              w="269px"
              h="28px"
              isInvalid={errors.name ? true : false}
              {...register("name", { required: true,
                pattern: {
                  value: inputValidation,
                  message: "スペースのみは入力できません",
                },
              })}
            />
          </Box>
          <Box marginBottom="15px">
            <Text fontSize="11px" fontWeight="bold">
              役職
            </Text>
            <Input w="269px" h="28px" {...register("position")} />
          </Box>
          <Box marginBottom="15px">
            <Flex>
              <Text fontSize="11px" fontWeight="bold">
                メールアドレス
              </Text>
              {errors.mail && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Input
              w="269px"
              h="28px"
              {...register("mail", {
                required: true,
                pattern: {
                  value: emailValidation,
                  message: "メールアドレスの形式で入力してください",
                },
              })}
              isInvalid={errors.mail ? true : false}
            />
            {errors.mail && (
              <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                {errors.mail.message}
              </Text>
            )}
          </Box>
          <Box marginBottom="15px">
            <Flex direction="row">
              <Text fontSize="11px" fontWeight="bold">
                電話番号（ハイフンなし）
              </Text>
              {errors.tell && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Input
              w="269px"
              h="28px"
              {...register("tell", {
                required: true,
                pattern: {
                  value: telValidation,
                  message: "電話番号は半角数字でハイフンなしで入力してください",
                },
              })}
              isInvalid={errors.tell ? true : false}
            />
            {errors.tell && (
              <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                {errors.tell.message}
              </Text>
            )}
          </Box>
          <Box marginBottom="15px">
            <Flex direction="row">
              <Text fontSize="11px" fontWeight="bold">
                郵便番号（ハイフンなし）
              </Text>
              {errors.postal && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Input
              w="269px"
              h="28px"
              {...register("postal", {
                required: true,
                pattern: {
                  value: postalValidation,
                  message: "ハイフンなしで入力してください",
                },
              })}
              isInvalid={errors.postal ? true : false}
              onChange={(e) => {
                const word = e.target.value;
                if (word.match(postalValidation)) {
                  axios
                    .get(
                      `https://madefor.github.io/postal-code-api/api/v1/${word.slice(
                        0,
                        3
                      )}/${word.slice(3, 7)}.json`
                    )
                    .then((response) => {
                      const data = response.data.data[0].ja;
                      setPrefectureValue(data.prefecture);
                      setAddressValue(
                        `${data.address1}${data.address2}${data.address3}${data.address4}`
                      );
                    });
                }
              }}
            />
          </Box>
          <Box marginBottom="15px">
            <Flex direction="row">
              <Text fontSize="11px" fontWeight="bold">
                都道府県
              </Text>
              {errors.prefecture && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Select
              w="269px"
              h="28px"
              {...register("prefecture", { required: true })}
              isInvalid={errors.prefecture ? true : false}
              value={prefectureValue}
              onChange={(e) => {
                setPrefectureValue(e.target.value);
              }}
              placeholder="選択してください"
            >
              {prefectures.map((prefecture) => {
                return (
                  <option value={`${prefecture}`}>{`${prefecture}`}</option>
                );
              })}
            </Select>
          </Box>
          <Box marginBottom="15px">
            <Flex direction="row">
              <Text fontSize="11px" fontWeight="bold">
                住所
              </Text>
              {errors.address && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Input
              w="269px"
              h="28px"
              {...register("address", { required: true,
                pattern: {
                  value: inputValidation,
                  message: "スペースのみは入力できません",
                },
              })}
              isInvalid={errors.address ? true : false}
              value={addressValue}
              onChange={(e) => {
                setAddressValue(e.target.value);
              }}
            />
          </Box>
          <Box marginBottom="15px">
            <Text fontSize="11px" fontWeight="bold">
              建物名
            </Text>
            <Input w="269px" h="28px" {...register("building")} />
          </Box>
        </Box>
        <Button
          w="260px"
          h="40px"
          marginTop="20px"
          color="#504E4E"
          bgColor="white"
          border="2px"
          borderColor="#504E4E"
          type="submit"
        >
          登録内容の確認
        </Button>
      </form>
    </Center>
    </div>
  );
};

export default CustomerRegistrationViewPage;
