import React from 'react';
import { useHistory } from 'react-router-dom';
import {
    Center,
    Text,
    Flex,
    Box,
    Spacer,
} from '@chakra-ui/react';
import axios from 'axios';
import HeaderSubBack from "../../components/HeaderSubBack"
import {apiURL,BatteryOption,BatteryOptionJson,Charger,ChargerJson,Units} from "../../modules/interfaces"
import BackButton from "../../components/buttons/BackButton";
import CarCard from "../../components/cards/CarCard";
import HeaderSub from 'components/HeaderSub';

interface CarDetailViewPageProps {
    location: {
        state:{
            company: string,
            officeName: string,
            contractName: string,
            contract_id: string,
            account_id: number,
            department_id: number,
            battery_option_id: number [],
            charger_id: number [],
            charger_name: string [],
            option_name: string [],
            charger_num: number [],
            option_num: number [],
            carName?: string,
            chargerName?: string,
            
        }
    }
}

const CarDetailViewPage = (props: CarDetailViewPageProps) => {
    const { location } = props;
    const company = location.state.company;
    const officeName = location.state.officeName;
    const contractName = location.state.contractName;
    const contract_id = location.state.contract_id;
    const account_id = location.state.account_id;
    const department_id = location.state.department_id;
    const carName = location.state.carName || "";
    const chargerName = location.state.chargerName || "";
    const battery_option_id = location.state.battery_option_id || [];
    const charger_id = location.state.charger_id || [];
    const charger_name = location.state.charger_name || [];
    const option_name = location.state.option_name || [];
    const charger_num = location.state.charger_num || [];
    const option_num = location.state.option_num || [];

    const history = useHistory();

    //battery optionsのうち、department idのものを取得（複数）
    const defaultOptions:BatteryOption[] = [];
    const [options, setOptions]: [BatteryOption[], (options: BatteryOption[]) => void] = React.useState(defaultOptions);

    React.useEffect(() => {
        axios
            .get<BatteryOption[]>(apiURL+"api/v1/customerbatteryoption/?department_id=" + department_id, {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setOptions(response.data);
            });
        }, []);
        console.log('battery options data should be fetched at this point. department id = '+ String(department_id))
        console.log(options)

    //info_typeがfieldかつ、contract_idが0のもののみを取得
    let fieldOptions:BatteryOption[] = [];
    if(options.length!=0){
        for (let i=0;i<options.length;i++){
            if(options[i].info_type=="field" && options[i].contract_id.Int32==0){
                fieldOptions[i]=options[i]
            }
        }
    }
    console.log('options in field status')
    console.log(fieldOptions)

    return (
        <div>
        <HeaderSubBack
            headerText = "車体情報の選択"
            onClick={() => {
                history.push({
                    pathname: `/battery-registration`,
                    state: {
                        company: company,
                        officeName: officeName,
                        contractName: contractName,
                        contract_id: contract_id,
                        account_id: account_id,
                        department_id: department_id,
                        battery_option_id: battery_option_id,
                        charger_id: charger_id,
                        option_name: option_name,
                        charger_name: charger_name,
                        option_num: option_num,
                        charger_num: charger_num,
                        //carName: carName,
                        //chargerName: chargerName,
                    }
                });
            }}
        />
        <Center flexDirection="column">
            <Text
                w="260px"
                fontSize="18px"
                fontWeight="bold"
                margin="30px"
            >
                バッテリーを載せる車体情報を選択してください
            </Text>
            {fieldOptions.map((value) => {
                let id = value.battery_option_id
                //let name = "車体"+String(id)
                let name = value.option_name
                //console.log(name)
                return(
                    <Box margin="10px">
                        <CarCard
                            name={name} //”車体"+string(battery_option_id)
                            car={value.option_name}
                            carType={value.forklift}
                            productNum={value.serial_number}

                            onClick= {() => {
                                //battery_option_id.push(id)
                                //option_name.push(name)
                                history.push({
                                    //pathname: `/battery-registration`,
                                    pathname: `/car-detail2`,
                                    state: {
                                        company: company,
                                        officeName: officeName,
                                        contractName: contractName,
                                        contract_id: contract_id,
                                        account_id: account_id,
                                        department_id: department_id,
                                        battery_option_id: battery_option_id,
                                        charger_id: charger_id,
                                        charger_name: charger_name,
                                        option_name: option_name,
                                        option_num: option_num,
                                        charger_num: charger_num,
                                        target_id: id,
                                        target_name: name,
                                        //carName: name,
                                        //chargerName: chargerName,
                                        //cahrger_id: 
                                    }
                                });
                            }}
                        />
                    </Box>
                );
            })}
            {/*
            <Flex w="260px" marginTop="50px" direction="row">
                <BackButton
                    onClick={() => {
                        history.push({
                            pathname: `/battery-registration`,
                            state: {
                                company: company,
                                officeName: officeName,
                                contractName: contractName,
                                contract_id: contract_id,
                                account_id: account_id,
                                department_id: department_id,
                                battery_option_id: battery_option_id,
                                charger_id: charger_id,
                                charger_name: charger_name,
                                //carName: carName,
                                //chargerName: chargerName,
                            }
                        });
                    }}
                />
                <Spacer/>
            </Flex>
                */}
        </Center>
        </div>
    );
}

export default CarDetailViewPage;