import React from "react";
import {
    Flex,
    Text,
    Center,
    Image
} from "@chakra-ui/react";

interface ChargerOptionCardProps {
    chargerOptionID: string,
    chargerOptionName: string,
    how2Supply: string,
    powerSupplyAmpere: number,
    stand: string,
    onClick?: (value: string) => void;
}

const ChargerOptionCard = (props: ChargerOptionCardProps) => {
    const {chargerOptionID, chargerOptionName, how2Supply, powerSupplyAmpere, stand, onClick} = props;

    let stand_str
    if (stand=="Need"){
        stand_str = "必要"
    }
    else{
        stand_str = "不要"
    }

    return (
        <Center
            w="267px"
            padding="10px"
            flexDirection="row"
            boxShadow="outline"
            style={{boxShadow: onClick !== undefined ?
                "2px 2px 4px rgba(0, 0, 0, 0.2)"
                : "0px 1px 0px rgba(0, 0, 0, 0.2)"
            }}
            onClick={() => {
                if (onClick !== undefined) {
                    onClick(chargerOptionID)
                }
            }}>

            <Flex direction="column" marginLeft="20px">
                <Text fontSize="14px" fontWeight="bold">{chargerOptionName}</Text>
                <Flex>
                    <Text fontSize="14px" fontWeight="bold" color="gray" w="100px">電源供給方法</Text>
                    <Text fontSize="14px" fontWeight="bold" w="150px">{how2Supply}</Text>
                </Flex>
                <Flex>
                    <Text fontSize="14px" fontWeight="bold" color="gray" w="100px">供給アンペア数</Text>
                    <Text fontSize="14px" fontWeight="bold" w="150px">{powerSupplyAmpere} A</Text>
                </Flex>
                <Flex>
                    <Text fontSize="14px" fontWeight="bold" color="gray" w="100px">架台</Text>
                    <Text fontSize="14px" fontWeight="bold" w="150px">{stand_str}</Text>
                </Flex>
            </Flex>
        </Center>
    );
}

export default ChargerOptionCard;