import React from "react";
import {
    Flex,
    Text,
    Center,
    Image
} from "@chakra-ui/react";

interface BatteryOptionCardProps {
    batteryOptionID: string,
    batteryOptionName: string,
    carType: string,//option_name
    type: string,
    serialNumber: string,
    onClick?: (value: string) => void;
}

const BatteryOptionCard = (props: BatteryOptionCardProps) => {
    const {batteryOptionID,batteryOptionName, carType, type, serialNumber, onClick} = props;

    return (
        <Center
            w="267px"
            padding="10px"
            flexDirection="row"
            boxShadow="outline"
            style={{boxShadow: onClick !== undefined ?
                "2px 2px 4px rgba(0, 0, 0, 0.2)"
                : "0px 1px 0px rgba(0, 0, 0, 0.2)"
            }}
            onClick={() => {
                if (onClick !== undefined) {
                    onClick(batteryOptionID)
                }
            }}>

            <Flex direction="column" marginLeft="10px">
                <Text fontSize="14px" fontWeight="bold">{batteryOptionName}</Text>
                <Flex>
                    <Text fontSize="14px" fontWeight="bold" color="gray" w="100px">車種</Text>
                    <Text fontSize="14px" fontWeight="bold" w="150px">{carType}</Text>
                </Flex>
                <Flex>
                    <Text fontSize="14px" fontWeight="bold" color="gray" w="100px">車体タイプ</Text>
                    <Text fontSize="14px" fontWeight="bold" w="150px">{type || ""}</Text>
                </Flex>
                <Flex>
                    <Text fontSize="14px" fontWeight="bold" color="gray" w="100px">車体番号</Text>
                    <Text fontSize="14px" fontWeight="bold" w="150px">{serialNumber}</Text>
                </Flex>
            </Flex>
        </Center>
    );
}

export default BatteryOptionCard;