import React from "react";
import {
    Flex,
    Text,
    Center,
} from "@chakra-ui/react";

interface ChargerCardProps {
    name: string,
    provideMethod: string,
    powerAmp: string,
    isNeedFrame: boolean,
    onClick?: () => void,
}

const ChargerCard = (props: ChargerCardProps) => {
    const {
        name,
        provideMethod,
        powerAmp,
        isNeedFrame,
        onClick,
    } = props;

    return (
        <Center
            w="267px"
            padding="10px"
            flexDirection="column"
            boxShadow="outline"
            style={{boxShadow: "2px 2px 4px rgba(0, 0, 0, 0.2)"}}
            onClick={() => {
                if (onClick !== undefined) {
                    onClick();
                }
            }}
        >
            <Text
                fontSize="18px"
                fontWeight="bold"
                marginBottom="5px"
            >
                充電器名：{name}
            </Text>
            <Flex direction="row">
                <Text
                    w="110px"
                    fontSize="14px"
                    fontWeight="bold"
                    color="#504E4E"
                >
                    供給方法
                </Text>
                <Text
                    w="110px"
                    fontSize="14px"
                    fontWeight="bold"
                    color="#504E4E"
                >
                    {provideMethod}
                </Text>
            </Flex>
            <Flex direction="row">
                <Text
                    w="110px"
                    fontSize="14px"
                    fontWeight="bold"
                    color="#504E4E"
                >
                    電源アンペア数
                </Text>
                <Text
                    w="110px"
                    fontSize="14px"
                    fontWeight="bold"
                    color="#504E4E"
                >
                    {powerAmp}
                </Text>
            </Flex>
            <Flex direction="row">
                <Text
                    w="110px"
                    fontSize="14px"
                    fontWeight="bold"
                    color="#504E4E"
                >
                    架台
                </Text>
                <Text
                    w="110px"
                    fontSize="14px"
                    fontWeight="bold"
                    color="#504E4E"
                >
                    {isNeedFrame ? "必要" : "不必要"}
                </Text>
            </Flex>
        </Center>
    );
}

export default ChargerCard;