import React from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Box, Text } from '@chakra-ui/react';

import ContractUnitCard from "../../components/cards/ContractUnitCard";

const UndefinedManuContractsPage = () => {

    const history = useHistory();

    //現場情報（battery_option）からinfo_type=現場情報のもの
    //->contract_idを取得->顧客id→nameを取得
    const dummyContracts = [
        {
            contractID: "0001",
            departmentID: "1234",
            customerID: "0001",
            contractName: "テスト契約",
            contractType: "テスト契約",
            customerName: "ROCKET BATTERY",
            departmentName: "A事務所",
            deadline: "2025/03/31",
        }
    ]

    return (
        <Center flexDirection="column">
            <Text
                w="290px"
                fontSize="22px"
                fontWeight="bold"
                margin="10px"
            >
                製造情報未確定契約一覧
            </Text>
            <Text
                w="290px"
                fontSize="12px"
                fontWeight="light"
                margin="10px"
            >
                以下から契約を選択し、製造情報を登録してください。
            </Text>

            <Box margin="15px">
                {dummyContracts.map((value) => {
                    return (
                        <Box marginTop="15px">
                            <ContractUnitCard
                                contractName={value.contractName}
                                contractID={value.contractID}
                                customerName={value.customerName}
                                departmentName={value.departmentName}
                                contractType={value.contractType}
                                deadLine={value.deadline}
                                onClick={()=>{
                                    //TODO:ここに遷移の処理をかく（stateの受け渡し有り）
                                    history.push({
                                        pathname: `/customer-field-info`,
                                        state: {
                                            customerID: value.customerID,
                                            contractID: value.contractID,
                                        }  
                                    });
                                }}
                            />
                        </Box>
                    );
                })}
            </Box>

        </Center>
    )
}

export default UndefinedManuContractsPage