import { Flex, Text, Spacer, Icon, Center } from "@chakra-ui/react";
import { MdError, MdBatteryStd, MdPower } from "react-icons/md";
import { BiCalendarAlt } from "react-icons/bi";

interface ContractCardProps {
  contractName: string;
  contractNum: string;
  contractType: string;
  startDate: string;
  endDate: string;
  batteryNum?: number;
  chargerNum?: number;
  onClick?: () => void;
}

const ContractCard = (props: ContractCardProps) => {
  const {
    contractName,
    contractNum,
    contractType,
    startDate,
    endDate,
    batteryNum,
    chargerNum,
    onClick,
  } = props;

  const isNeedRequest = batteryNum && chargerNum ? false : true;

  return (
    <Center
      w="234px"
      h="173px"
      flexDirection="column"
      color="#504E4E"
      boxShadow="outline"
      style={{
        boxShadow: "0px 4px 5px rgba(0, 0, 0, 0.5)",
      }}
      onClick={onClick}
    >
      <Flex
        w="200px"
        h="22px"
        direction="row"
        visibility={isNeedRequest ? "visible" : "hidden"}
        color="#EB5757"
        border="1px"
        borderColor="#EB5757"
        borderRadius="1px"
        alignItems="center"
        justifyContent="space-around"
      >
        <Icon as={MdError} />
        <Text fontSize="10px">製造リクエストを作成してください</Text>
      </Flex>
      <Text w="190px" fontSize="18px" fontWeight="bold" marginTop="8px">
        {contractName}
      </Text>
      <Text w="190px" fontSize="10px">
        {contractNum}
      </Text>
      <Text w="190px" fontSize="12px">
        {contractType}
      </Text>
      <Flex
        w="170px"
        direction="row"
        fontSize="12px"
        fontWeight="bold"
        alignItems="center"
        marginTop="5px"
      >
        <Icon w="17px" h="17px" as={BiCalendarAlt} />
        <Text marginLeft="3px">{startDate}</Text>
        <Text marginLeft="3px">-</Text>
        <Text marginLeft="3px">{endDate}</Text>
      </Flex>
      <Flex
        w="200px"
        direction="row"
        fontWeight="bold"
        justifyContent="space-around"
        marginTop="3px"
      >
        <Flex
          w="75px"
          direction="row"
          alignItems="center"
          justifyContent="space-around"
        >
          <Icon w="32px" h="32px" as={MdBatteryStd} />
          <Text fontSize="24px">{batteryNum ? batteryNum : "-"}</Text>
          <Text fontSize="14px" alignSelf="flex-end">
            台
          </Text>
        </Flex>
        <Flex
          w="75px"
          direction="row"
          alignItems="center"
          justifyContent="space-around"
        >
          <Icon w="32px" h="32px" as={MdPower} />
          <Text fontSize="24px">{chargerNum ? chargerNum : "-"}</Text>
          <Text fontSize="14px" alignSelf="flex-end">
            台
          </Text>
        </Flex>
      </Flex>
    </Center>
  );
};

export default ContractCard;
