import React, {useState} from "react";
import {
    Flex,
    Text,
    Center,
} from "@chakra-ui/react";
import {ThemeColor} from "../../utils/ThemeColor"

interface ContractCardProps {
    //contractNum: number,
    contractName: string
    contractID: string,
    contractType: string,
    deadLine: string,
    //unitNum: number,
    selectable?: boolean,
    onClick?: (value: string) => void,
}

const ContractCard = (props: ContractCardProps) => {
    const [isSelectedState, setIsSelectedState] = useState(false);
    const {
        //contractNum,
        contractName,
        contractID,
        contractType,
        deadLine,
        //unitNum,
        selectable,
        onClick
    } = props;

    return (
        <Center
            w="267px"
            padding="20px"
            flexDirection="column"
            borderColor={isSelectedState ? ThemeColor.robotColor : "white"}
            borderWidth="6px"
            boxShadow="outline"
            style={{boxShadow: onClick !== undefined ?
                "2px 2px 4px rgba(0, 0, 0, 0.2)"
                : "0px 1px 0px rgba(0, 0, 0, 0.2)"
            }}
            onClick={() => {
                if (onClick !== undefined) {
                    if (selectable) {
                        setIsSelectedState(!isSelectedState);
                    }
                    onClick(contractID)
                }
            }}>
            <Text fontSize="18px" fontWeight="bold" marginBottom="15px">{contractName}</Text>
            <Flex>
                <Text fontSize="14px" fontWeight="bold" color="gray" w="80px">契約ID</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{contractID}</Text>
            </Flex>
            <Flex>
                <Text fontSize="14px" fontWeight="bold" color="gray" w="80px">契約タイプ</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{contractType}</Text>
            </Flex>
            <Flex>
                <Text fontSize="14px" fontWeight="bold" color="gray" w="80px">契約終了日</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{deadLine}</Text>
            </Flex>
            {/*
            <Flex>
                <Text fontSize="14px" fontWeight="bold" color="gray" w="80px">ユニット数</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{unitNum}</Text>
            </Flex>
            <Text fontSize="14px" fontWeight="bold" color="#BB2C2C">ユニットを追加してください</Text>
            */}
        </Center>
    );
}

export default ContractCard;