import React from "react";
import {Center, Button, Image, Flex, Box} from "@chakra-ui/react";
import { useAuth0 } from "@auth0/auth0-react";

import logo from "../assets/MICO_logo.png"
import {useWindowDimensions} from "../utils/WindowDimensions";

const SingInPage = () => {
    let {width, height} = useWindowDimensions();
    const { loginWithRedirect } = useAuth0();

    return (
        <Center w={width} h={height} bgColor="white">
            <Flex direction="column">
                <Box>
                    <Image src={logo}/>
                </Box>
                <Button
                    marginTop="100px"
                    color="black"
                    border="2px"
                    bgColor="white"
                    borderColor="black"
                    onClick={loginWithRedirect}
                >
                    SIGN IN
                </Button>
            </Flex>
        </Center>
    );
}

export default SingInPage;