import {useRef} from "react";
import { Text, Image, Center, Flex, Input, Button } from "@chakra-ui/react";
import { SearchIcon } from '@chakra-ui/icons'
import {ThemeColor} from "../../utils/ThemeColor";

interface SearchMenuProps {
    onSearchHandler: (value: string) => void,
}

const SearchMenu = (props: SearchMenuProps) => {
    const inputRef = useRef<HTMLInputElement | null>(null);

    return (
        <Center
            w="317px"
            h="100px"
            padding="15px"
            bgColor="#EEEEEE"
            borderRadius="4px"
        >
            <SearchIcon w="28px" h="28px"/>
            <Flex marginLeft="20px" direction="column">
                <Text w="235px" fontSize="sm" color="#000000">
                    UNIT IDを入力してバッテリーの詳細を確認する。
                </Text>
                <Flex>
                    <Input
                        ref={inputRef}
                        w="181px"
                        h="28px"
                        color="#000000"
                        bgColor="#FFFFFF"
                        placeholder="UINT ID"
                    />
                    <Button
                        w="54px"
                        h="28px"
                        color="#000000"
                        bgColor={ThemeColor.robotColor}
                        onClick={() => {
                            props.onSearchHandler(inputRef.current?.value ?? "");
                        }}
                    >
                        GO
                    </Button>
                </Flex>
            </Flex>
        </Center>
    );
}

export default SearchMenu;