import React from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Box, Text } from '@chakra-ui/react';

import SearchBar from "../../components/SearchBar";
import BackButton from "../../components/buttons/BackButton";
import UnProductedBatteryCard from "../../components/cards/UnProductedBatteryCard";

interface UnProductedBatteriesPageProps {
    location: {
        state: {
            batteryID: string,
        }
    }
}

const UnProductedBatteriesPage = (props: UnProductedBatteriesPageProps) => {

    const {location} = props
    const batteryID = location.state.batteryID

    //unitsを全て確認し、batteriesに含まれないものを取得
    //unitIDを元にsoc取得＆isOn計算
    const dummyData = [
        {
            unitID: "1112395879900000",
            soc: 20,
            isOn: true,
        },
        {
            unitID: "1112393345200000",
            soc: 20,
            isOn: true,
        },
    ];

    const history = useHistory();

    return (
        <Center flexDirection="column">
            <Text
                w="290px"
                fontSize="22px"
                fontWeight="bold"
                margin="10px"
            >
                未登録バッテリー一覧
            </Text>
            <Box>
                <SearchBar
                    onChange={()=>{
                        //TODO:フィルタリングの処理
                    }}
                />
            </Box>

            {dummyData.map((value) => {
                return (
                    <Box margin="15px">
                        <UnProductedBatteryCard
                            remainingAmount={value.soc}
                            unitID = {value.unitID}
                            isOn={value.isOn}
                            onClick={()=>{
                                //次ページでbatteryIDにunitIDを紐付け＆batteryOptionの製造情報->製品情報に
                                history.push({
                                    pathname: '/product-registration',
                                    state: {
                                        batteryID: batteryID,
                                        unitID: value.unitID
                                    }
                                });
                            }}
                        />
                    </Box> 
                );
            })}

            <Box margin = "10px"><BackButton
                    onClick={()=>{
                        history.push({
                            pathname: '/unmanu-view',
                            state: {
                            }
                        });
                    }}
            /></Box>
        </Center>
    )
}

export default UnProductedBatteriesPage