const HomePage = () => {

    interface notion{
        massage: string,
        massage2: string,
        color: string,
    }

    //battery requestの取得
    const defaultRequests:BatteryRequest[] = [];
    const [requests, setRequests]: [BatteryRequest[], (requests: BatteryRequest[]) => void] = React.useState(defaultRequests);
    React.useEffect(() => {
        axios
            .get<BatteryRequest[]>(apiURL+"api/v1/batteryrequests/", {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setRequests(response.data);
            });
        }, []);
        console.log('battery requests data should be fetched at this point.')
    
    //info_typeがfieldかつcontract_idが0「以外」のもののみを取得
    let fieldRequests:BatteryRequest[] = [];
    if(requests!=null && requests.length!=0){
        for (let i=0;i<requests.length;i++){
            if(requests[i].contract_id!=0){
                fieldRequests.push(requests[i])
            }
        }
    }

    //充電器データの取得
    //charger optionデータの取得
    const defaultChargers:Charger[] = [];
    const [chargers, setChargers]: [Charger[], (chargers: Charger[]) => void] = React.useState(defaultChargers);

    React.useEffect(() => {
        axios
            .get<Charger[]>(apiURL+"api/v1/chargers/", {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setChargers(response.data);
            });
        }, []);
        console.log('chargers data should be fetched at this point.');
        //console.log(chargers)

    //info_typeがfieldかつcontract_idが0「以外」のもののみを取得
    let fieldChargers:Charger[] = [];
    if(chargers!=null && chargers.length!=0){
        for (let i=0;i<chargers.length;i++){
            if(chargers[i].info_type=="field" && chargers[i].contract_id.Int32!=0){
                fieldChargers.push(chargers[i])
            }
        }
    }
    //console.log(fieldChargers)

    //契約情報を取得
    const defaultContracts:ContractsHome[] = []
    const [contracts, setContracts]: [ContractsHome[], (contracts: ContractsHome[]) => void] = React.useState(defaultContracts);
    React.useEffect(() => {
        axios
            .get<ContractsHome[]>(apiURL+"api/v1/contractshome/", {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setContracts(response.data);
            });
        }, []);
        console.log('contract data should be fetched at this point');
        console.log(contracts)

    //battery_request情報のidが一致するもののみを取得
    let errorContract:ContractsHome[] = [];
    let ids = []
    if(contracts!=null && contracts.length>0 && fieldRequests.length>0){
        for (let i=0;i<contracts.length;i++){
            for (let ii=1;ii<fieldRequests.length;ii++){
                //console.log(contracts[i].contract_id)
                if(contracts[i].contract.contract_id==fieldRequests[ii].contract_id){
                    ids.push(contracts[i].contract.contract_id)
                    break
                }
            }
            for (let ii=1;ii<fieldChargers.length;ii++){
                if(contracts[i].contract.contract_id==fieldChargers[ii].contract_id.Int32){
                    ids.push(contracts[i].contract.contract_id)
                    break
                }
            }
        }
    }
    let set_ids = Array.from(new Set(ids))
    //console.log(set_ids)
    //リクエスト情報のあるもののみを取り出す
    if(contracts!=null && contracts.length>0 && set_ids.length>0){
        for (let i=0;i<contracts.length;i++){
            if (set_ids.indexOf(contracts[i].contract.contract_id)!=-1){//存在しなければ-1
                errorContract[i]=contracts[i]
            }
        }
    }

    let notion:notion[]=[]
    let cnt = 0
    if ((requests!=null && requests.length>0) || fieldChargers.length>0){
        cnt++
    }

    if (cnt>0){
        notion.push({massage:"製造リクエスト未作成の契約",massage2:"作成された契約の中で、製造リクエストが未作成のものがあります。\n リクエスト作成を完了させて、工場での製造を開始できるようにしてください",color:"black"})
    }else{
        notion.push({massage:"",massage2:"",color:"black"})
    }

    let notion2:notion[]=[]
    let cnt2 = 0

    if (cnt2>0){
        notion2.push({massage:"終了日の近い契約",massage2:"以下の契約で終了日が近づいています",color:"black"})
    }else{
        notion2.push({massage:"",massage2:"",color:"black"})
    }

    const history = useHistory();

    //errorがあるかどうかchk
    const iconname = "default"

    return(
        <div>
        <Header
          drawerItems={[
            {
              title: "ホーム",
              path: "/",
            },
            {
              title: "バッテリー",
              path: "/units",
            },
            {
              title: "お客様",
              path: "/customer-view",
            },
          ]}
          //iconName={iconname}
        />
        <Center flexDirection="column">
            <Box margin="15px">
                <Box marginTop="10px">
                    <AddButton
                        label="新しい契約を作成"
                        onClick={() => {
                            history.push(`/contract-home`);
                        }}
                    />
                    {/*
                    <AddContractMenu onClick={()=>{
                        history.push(`/contract-home`);
                    }}/>
                    */}
                </Box>
                {/*
                <Box marginTop="10px">
                    <AddCustomerMenu onClick={()=>{
                        history.push(`/customer-view`);
                    }}/>
                </Box>
                */}
                {/*
                <Box marginTop="10px">
                    <SearchMenu onSearchHandler={()=>{
                        //TODO:検索して遷移の実装
                    }}/>
                </Box>
                */}
            </Box>
            {/*<Divider/>*/}
            {notion.map((value) => {
                return(
                    <Box>
                    <Text fontWeight="bold" padding="15px" marginTop="10px" marginBottom="0px" marginLeft="30px" marginRight="30px" color={value.color} fontSize="16px">
                        {value.massage}
                    </Text>
                    <Text marginTop="-20px" padding="15px" marginBottom="0px" marginLeft="30px" marginRight="30px" color={value.color} fontSize="10px">
                        {value.massage2}
                    </Text>
                    </Box>
                );
            })}

            {errorContract.map((value) => {
                    let date1:Date = new Date(String(value.contract.execution_date))
                    let date2:Date = new Date(String(value.contract.expiration_date))
                    return (
                        <ContractCard
                            contractName={value.contract.contract_name}
                            contractNum={String(value.contract.contract_id)}
                            contractType={value.contract.contract_type}
                            startDate={formatDate(date1,DateFormat.YY_MM_DD_dd)}
                            endDate={formatDate(date2,DateFormat.YY_MM_DD_dd)}
                            //TODO on click
                            
                            onClick={() => {
                                history.push({
                                    pathname: `/contract-unit`,
                                    state: {
                                        account_id: value.account_id,
                                        contract_id: value.contract.contract_id,
                                        //contract_list:contract_list,
                                    }
                                });
                            }}
                            
                        />
                    );
                })}

            {/*
            {fieldRequests.map((value) => {
                return(
                    <Box margin = "5px">
                        <CarOrderCard
                            name={value.option_name} //”車体"+string(battery_option_id)
                            //option_name={value.option_name}
                            department_name={value.department_name}
                            contract_name={value.contract_name}
                        />
                    </Box>
                );
            })}
            
            {fieldChargers.map((value) => {
                let id = value.charger_id
                //let name = "車体"+String(id)
                let name = value.charger_name
                let frame = false
                if (value.stand=="need"){
                    frame = true
                }
                return(
                    <Box margin = "5px">
                        <ChargerCard
                            name={name}
                            provideMethod={value.how2supply}
                            powerAmp={String(value.power_supply_ampere.Int32)}
                            isNeedFrame={frame}
                        />
                    </Box>
                );
            })}*/}

            <Box margin="10px"></Box>
            <Divider/>
            {notion2.map((value) => {
                return(
                    <Box>
                    <Text fontWeight="bold" padding="15px" marginTop="10px" marginBottom="0px" marginLeft="30px" marginRight="30px" color={value.color} fontSize="16px">
                        {value.massage}
                    </Text>
                    <Text marginTop="-20px" padding="15px" marginBottom="0px" marginLeft="30px" marginRight="30px" color={value.color} fontSize="10px">
                        {value.massage2}
                    </Text>
                    </Box>
                );
            })}
            
            <Box margin="15px">
                {/*
                {error_units.map((value) => {
                    return (
                        <Box marginTop="15px">
                            <BatteryCard
                                remainingAmount={value.soc}
                                unitID={value.unit_id}
                                batteryType={value.profile.unit_type}
                                company={value.customer_name}
                                purpose={value.profile.purpose}
                                requiredAction={value.required_action}
                                onClick={()=>{
                                    history.push({
                                        pathname: `/unit-detail`,
                                        state: {
                                            contractID: value.contract_id,
                                            unitID: value.unit_id
                                        }
                                    });
                                }}
                            />
                        </Box>
                    );
                })}
                */}
            </Box>
        </Center>
        </div>
    );
};

export default HomePage;