import React from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Box, Text, Button } from '@chakra-ui/react';

import Header from "../../components/Header";
import CompanyCard from "../../components/cards/CompanyCard";
import OfficeCard from "../../components/cards/OfficeCard";
import StatusBar from "../../components/StatusBar";

interface CustomerDepartmentRegistrationCompleteViewPageProps {
    location: {
        state: {
            company: string,
            officeName: string,
            account_id: number,
            department_id: number,
        }
    }
}

const CustomerDepartmentRegistrationCompleteViewPage = (props: CustomerDepartmentRegistrationCompleteViewPageProps) => {
    const { location } = props;
    const company = location.state.company;
    const officeName = location.state.officeName;
    const account_id = location.state.account_id;
    const department_id = location.state.department_id;

    const history = useHistory();

    return (
        <div>
        <Header
          drawerItems={[
            {
              title: "ホーム",
              path: "/",
            },
            {
              title: "バッテリー",
              path: "/units",
            },
            {
              title: "お客様",
              path: "/customer-view",
            },
          ]}
          //iconName={iconname}
        />
        <Center flexDirection="column">
            <Text
                marginTop="40px"
                fontSize="18px"
                fontWeight="bold"
                marginBottom= "30px"
            >
                顧客/事業所登録が完了しました！
            </Text>
            <Box w="269px" marginBottom="15px">
                <Text fontSize="16px" fontWeight="bold">
                    企業名
                </Text>
                <Text fontSize="16px" fontWeight="bold">
                    {company}
                </Text>
            </Box>
            <Box marginTop="20px">
                <OfficeCard
                    officeName={officeName}
                />
            </Box>
            <Text
                fontSize="14px"
                fontWeight="bold"
                marginTop="40px"
            >
                次に現場情報を登録してください
            </Text>
            <StatusBar activeStep={1}/>
            <Center flexDirection="column">
                <Button
                    w="225px"
                    h="40px"
                    marginTop="40px"
                    color="#504E4E"
                    bgColor="white"
                    border="2px"
                    borderColor="#504E4E"
                    onClick={() => {
                        history.push({
                            pathname: '/department-registration',
                            state: {
                                company: company,
                                officeName: officeName,
                                account_id: account_id,
                                department_id: department_id,
                            }
                        });
                    }}
                >
                    続けて現場情報を登録する
                </Button>
                {/*
                <Button
                    w="158px"
                    h="40px"
                    marginTop="20px"
                    color="#504E4E"
                    bgColor="white"
                    border="2px"
                    borderColor="#504E4E"
                    onClick={() => {
                        history.push(`/customer-view`);
                    }}
                >
                    顧客一覧へ
                </Button>
                */}
            </Center>
        </Center>
        </div>
    );
}

export default CustomerDepartmentRegistrationCompleteViewPage;