import { Flex, Text, Spacer, Icon, Center } from "@chakra-ui/react";
import { MdDirectionsCar } from "react-icons/md";

interface VehicleFieldCardProps {
  vehicleName: string;
  liftType: string;
  volt: number;
  capacity: number;
  weight: number;
  depth: number;
  width: number;
  height: number;
  onClick?: (chargerName: string) => void;
}

const VehicleFieldCard = (props: VehicleFieldCardProps) => {
  const {
    vehicleName,
    liftType,
    volt,
    capacity,
    weight,
    depth,
    width,
    height,
    onClick,
  } = props;

  return (
    <Center
      w="267px"
      h="144px"
      flexDirection="column"
      boxShadow="outline"
      style={{
        boxShadow: "0px 4px 5px rgba(0, 0, 0, 0.5)",
      }}
      onClick={() => {
        if (onClick !== undefined) {
          onClick(vehicleName);
        }
      }}
    >
      <Flex direction="row" alignItems="center">
        <Icon w="35px" h="35px" as={MdDirectionsCar} />
        <Flex direction="column">
          <Text fontSize="18px" fontWeight="bold">
            {vehicleName}
          </Text>
          <Text fontSize="9px" fontWeight="lift">
            {`フォークリフト-${liftType}`}
          </Text>
        </Flex>
      </Flex>
      <Center w="225px" flexDirection="row" fontSize="8px" marginTop="10px">
        <Center flexDirection="column" marginRight="8px">
          <Text>電圧</Text>
          <Text fontWeight="bold" fontSize="12px">{`${volt}V`}</Text>
          <Text marginTop="8px">前後幅</Text>
          <Text fontWeight="bold" fontSize="12px">{`${depth}mm`}</Text>
        </Center>
        <Center flexDirection="column" marginRight="8px">
          <Text>容量</Text>
          <Text fontWeight="bold" fontSize="12px">{`${capacity}Ah`}</Text>
          <Text marginTop="8px">左右幅</Text>
          <Text fontWeight="bold" fontSize="12px">{`${width}mm`}</Text>
        </Center>
        <Center flexDirection="column">
          <Text>重量</Text>
          <Text fontWeight="bold" fontSize="12px">{`${weight}kg`}</Text>
          <Text marginTop="8px">高さ</Text>
          <Text fontWeight="bold" fontSize="12px">{`${height}mm`}</Text>
        </Center>
      </Center>
    </Center>
  );
};

export default VehicleFieldCard;
