import React from "react";
import {
    Flex,
    Text,
    Center,
    Image
} from "@chakra-ui/react";
import rocket_icon from "../../assets/rocket_icon.png";

interface CompanyCardProps {
    iconURL?: string,
    companyName: string,
    customerID: string,
    jobType?: string,
    onClick?: (value: string) => void;
}

const CompanyCard = (props: CompanyCardProps) => {
    const {iconURL, companyName, customerID, jobType, onClick} = props;

    return (
        <Center
            w="267px"
            padding="10px"
            flexDirection="row"
            boxShadow="outline"
            style={{boxShadow: onClick !== undefined ?
                "2px 2px 4px rgba(0, 0, 0, 0.2)"
                : "0px 1px 0px rgba(0, 0, 0, 0.2)"
            }}
            onClick={() => {
                if (onClick !== undefined) {
                    onClick(customerID)
                }
            }}>
            <Image w="63px" src={iconURL || ""}/>
            <Flex direction="column" marginLeft="20px">
                <Flex>
                    <Text fontSize="14px" fontWeight="bold" color="gray" w="50px">企業名</Text>
                    <Text fontSize="14px" fontWeight="bold">{companyName}</Text>
                </Flex>
                <Flex>
                    <Text fontSize="14px" fontWeight="bold" color="gray" w="50px">企業ID</Text>
                    <Text fontSize="14px" fontWeight="bold" w="100px">{customerID}</Text>
                </Flex>
                <Flex>
                    <Text fontSize="14px" fontWeight="bold" color="gray" w="50px">業種</Text>
                    <Text fontSize="14px" fontWeight="bold" w="100px">{jobType || ""}</Text>
                </Flex>
            </Flex>
        </Center>
    );
}

export default CompanyCard;