import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { useHistory } from "react-router-dom";
import {
  Center,
  Box,
  Text,
  Input,
  Menu,
  MenuButton,
  MenuList,
  MenuItem,
  Button,
  Flex,
  Select,
} from "@chakra-ui/react";
import { ChevronDownIcon } from "@chakra-ui/icons";
import HeaderSubBack from "../../components/HeaderSubBack";

import {
  inputValidation,
  numValidation,
} from "../../utils/validations";

import { contracttypes } from "../../utils/selection";

type FormInputs = {
  contractName: string;
  contractStartDate: string;
  contractEndDate: string;
  contractType: string;
};

interface ContractRegistrationViewPageProps {
  location: {
    state: {
      company: string; //corporation name
      officeName: string;
      account_id: number;
      department_id: number;
      is_update: boolean;
      contractData?: {
        contractName: string;
        contractStartDate: string;
        contractEndDate: string;
        contractType: string;
      };
    };
  };
}

const ContractRegistrationViewPage = (
  props: ContractRegistrationViewPageProps
) => {
  const { location } = props;
  const company = location.state.company;
  const officeName = location.state.officeName;
  const account_id = location.state.account_id;
  const department_id = location.state.department_id;
  const is_update= location.state.is_update;
  //console.log(department_id)
  const [contractTypeInputState, setContractTypeInputState] = useState("");

  const {
    register,
    formState: { errors },
    handleSubmit,
  } = useForm<FormInputs>({
    defaultValues: {
      contractName: location.state.contractData?.contractName || "",
      contractStartDate: location.state.contractData?.contractStartDate || "",
      contractEndDate: location.state.contractData?.contractEndDate || "",
      contractType: location.state.contractData?.contractType || "",
    },
  });

  const onSubmit = (data: FormInputs) => {
    history.push({
      pathname: `/contract-review`,
      state: {
        company: company,
        officeName: officeName,
        account_id: account_id,
        department_id: department_id,
        contractData: {
          contractName: data.contractName,
          contractStartDate: data.contractStartDate,
          contractEndDate: data.contractEndDate,
          contractType: data.contractType,
        },
      },
    });
  };

  const history = useHistory();

  /*
  const parseAsMoment = (dateTimeStr) => {
    return moment.utc(dateTimeStr, 'YYYY-MM-DDTHH:mm:00Z', 'ja').utcOffset(9)
  }

  const toUtcIso8601str = (momentInstance) => {
    return momentInstance
      .clone()
      .utc()
      .format('YYYY-MM-DDTHH:mm:00Z')
  }

  const CustomInputDatePicker = () => {
    const [startDate, setStartDate] = useState(toUtcIso8601str(moment()))
    const handleChange = (selectedDate) => {
      setStartDate(toUtcIso8601str(moment(selectedDate)))
    }
    */

  return (
    <div>
        <HeaderSubBack
          headerText="新規契約の登録"
          onClick={() => {
            /*
            history.push({
              pathname: "/department-registration",
              state: {
                company: company,
                officeName: officeName,
                account_id: account_id,
                department_id: department_id,
                is_update: is_update,
            },
            });
            */
            history.goBack();
          }}
        />
    <Center padding="15px" flexDirection="column">
      <form onSubmit={handleSubmit(onSubmit)}>
        <Box margin="20px">
          <Text w="260px" fontSize="18px" fontWeight="bold">
            契約情報を入力してください
          </Text>
        </Box>
        <Box margin="20px">
          <Box marginBottom="15px">
            <Text
              w="260px"
              fontSize="16px"
              fontWeight="bold"
              marginBottom="5px"
              color="#504E4E"
            >
              企業名
            </Text>
            <Text w="260px" fontSize="16px" fontWeight="bold" marginBottom="15px">
              {company}
            </Text>
            <Text
              w="260px"
              fontSize="16px"
              fontWeight="bold"
              marginBottom="5px"
              color="#504E4E"
            >
              事業所名
            </Text>
            <Text w="260px" fontSize="16px" fontWeight="bold" marginBottom="20px">
              {officeName}
            </Text>
            </Box>
            <Box marginBottom="15px">
            <Flex
              direction="row"
              //w="260px"
              fontSize="11px"
              fontWeight="bold"
              marginBottom="5px"
              color="#504E4E"
            >
              <Text>契約名</Text>
              {errors.contractName && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            </Box>
            <Input
              w="260px"
              marginBottom="15px"
              placeholder="例) テスト契約"
              isInvalid={errors.contractName ? true : false}
              {...register("contractName", { required: true ,
                pattern: {
                  value: inputValidation,
                  message: "スペースのみは入力できません",
                },
              })}
            />
            {errors.contractName && (
              <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                {errors.contractName.message}
              </Text>
            )}
            <Flex
              direction="row"
              //w="260px"
              fontSize="11px"
              fontWeight="bold"
              marginBottom="5px"
              color="#504E4E"
            >
              <Text>契約開始年月</Text>
              {errors.contractStartDate && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Input
              w="260px"
              marginBottom="15px"
              type = "date"
              //placeholder="例) 2021/04/01"
              isInvalid={errors.contractStartDate ? true : false}
              {...register("contractStartDate", { required: true })}
            />
            <Flex
              direction="row"
              //w="260px"
              fontSize="11px"
              fontWeight="bold"
              marginBottom="5px"
              color="#504E4E"
            >
              <Text>契約終了年月</Text>
              {errors.contractEndDate && (
                <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                  必須
                </Text>
              )}
            </Flex>
            <Input
              w="260px"
              marginBottom="15px"
              type = "date"
              //placeholder="例) 2023/03/31"
              isInvalid={errors.contractEndDate ? true : false}
              {...register("contractEndDate", { required: true })}
            />

          <Flex
            direction="row"
            w="260px"
            fontSize="11px"
            fontWeight="bold"
            color="#504E4E"
            marginBottom="10px"
          >
            <Text>契約タイプ</Text>
            {errors.contractType && (
              <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                必須
              </Text>
            )}
          </Flex>
          <Select
              w="260px"
              h="28px"
              marginBottom="20px"
              {...register("contractType", { required: true })}
              isInvalid={errors.contractType ? true : false}
              value={contractTypeInputState}
              onChange={(e) => {
                setContractTypeInputState(e.target.value);
              }}
              placeholder="選択してください"
            >
              {contracttypes.map((contractType) => {
                return (
                  <option value={`${contractType}`}>{`${contractType}`}</option>
                );
              })}
            </Select>

        </Box>
        <Button
          w="260px"
          h="40px"
          margin="20px"
          color="#504E4E"
          bgColor="white"
          border="2px"
          borderColor="#504E4E"
          type="submit"
        >
          登録内容を確認
        </Button>
      </form>
    </Center>
    </div>
  );
};

export default ContractRegistrationViewPage;
