import React from "react";
import { useHistory } from 'react-router-dom';
import {Center, Box, Text, Flex} from "@chakra-ui/react";

import {useWindowDimensions} from "../../utils/WindowDimensions";
import BackButton from "../../components/buttons/BackButton";

interface CustomerBatteryOptionPageProps {
    location: {
        state: {
            customerID: string,
            batteryOptionID: string,
            departmentID: string,
        }
    }
}

const CustomerBatteryOptionPage = (props: CustomerBatteryOptionPageProps) => {
    const {location} = props
    let {width, height} = useWindowDimensions();
    const batteryOptionID = location.state.batteryOptionID
    const departmentID = location.state.departmentID
    const customerID = location.state.customerID

    const history = useHistory();

    //batteryOptionIDを元にゲトる
    const dummyBatteryOptionData = {
        batteryOptionName: "車体A",       
    }

    return(
        <Center w={width} flexDirection="column">
            <Text
                fontSize="22px"
                fontWeight="bold"
                margin="10px"
            >
                現場詳細情報
            </Text>
            <Text>
                {dummyBatteryOptionData.batteryOptionName}
            </Text>
            <Flex direction="row">
                <Text color="#505050" fontSize="12px" fontWeight="bold" w="146px">
                    TBD
                </Text>
                <Text fontSize="12px" fontWeight="bold" w="168px">
                    TBD
                </Text>
            </Flex>
            <Box margin = "10px"><BackButton
                onClick={()=>{
                    history.push({
                        pathname: '/customer-department',
                        state: {
                            customerID: customerID,
                            departmentID: departmentID,
                        }
                    });
                }}
            /></Box>
        </Center>
    )
}

export default CustomerBatteryOptionPage