import React from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Box, Button, Text, Input, Divider } from '@chakra-ui/react';

import CompanyCard from "../../components/cards/CompanyCard";
import StatusBar from "../../components/StatusBar";
import HeaderSub from 'components/HeaderSub';

interface CustomerRegistrationCompleteViewPageProps {
    location: {
        state:{
            company: string,
            jobType?: string,
            account_id: number,
        }
    }
}

const CustomerRegistrationCompleteViewPage = (props: CustomerRegistrationCompleteViewPageProps) => {
    const { location } = props;
    const company = location.state.company;
    const jobType = location.state.jobType;
    const account_id = location.state.account_id;
    //console.log("props account_id is:"+account_id)

    const history = useHistory();

    return (
        <div>
        <HeaderSub
          headerText="新規顧客登録完了"
          />
        <Center flexDirection="column">
            <Text
                marginTop="40px"
                fontSize="18px"
                fontWeight="bold"
            >
                顧客登録が完了しました！
            </Text>
            <Divider/>
            <Box marginTop="20px">
                <CompanyCard
                    companyName={company}
                    //TODO:発行されたIDとiconのセットを行う
                    //customerID="1234"
                    customerID={String(account_id)}
                    iconURL=""
                    jobType={jobType}
                />
            </Box>
            <Text
                fontSize="14px"
                fontWeight="bold"
                marginTop="40px"
            >
                次に事業所を登録してください
            </Text>
            <StatusBar activeStep={0}/>
            <Button
                w="198px"
                h="40px"
                marginTop="40px"
                color="#504E4E"
                bgColor="white"
                border="2px"
                borderColor="#504E4E"
                onClick={() => {
                    history.push({
                        pathname: '/department-view',
                        state: {
                            company: company,//corporation_name
                            account_id: account_id
                        }
                    });
                }}
            >
                事業所登録へ
            </Button>
        </Center>
        </div>
    );
}

export default CustomerRegistrationCompleteViewPage;