import React from "react";
import {
    Flex,
    Text,
    Center,
    Image
} from "@chakra-ui/react";
import battery_ful from "../../assets/battery-full.png";
import battery_80 from "../../assets/battery-80.png";
import battery_30 from "../../assets/battery-30.png";
import battery_20 from "../../assets/battery-20.png";

interface UnproductedBatteryCardProps {
    remainingAmount: number,//soc
    unitID: string,//bmsシリアルnum
    isOn: boolean,//DBのタイムスタンプからバックエンドで計算
    onClick?: (value: string) => void;
}

const UnproductedBatteryCard = (props: UnproductedBatteryCardProps) => {
    const {remainingAmount, unitID, isOn, onClick} = props;

    const getBatteryLogo = () => {
        if (remainingAmount === 100) {
            return battery_ful;
        } else if (remainingAmount >= 80) {
            return battery_80;
        } else if (remainingAmount >= 30) {
            return battery_30;
        } else if (remainingAmount >= 20) {
            return battery_20;
        } else {
            return "";
        }
    };

    const batteryLogo = getBatteryLogo();

    return (
        <Center
            w="267px"
            padding="10px"
            flexDirection="column"
            boxShadow="outline"
            style={{boxShadow: onClick !== undefined ?
                "2px 2px 4px rgba(0, 0, 0, 0.2)"
                : "0px 1px 0px rgba(0, 0, 0, 0.2)"
            }}
            onClick={() => {
                if (onClick !== undefined) {
                    onClick(unitID);
                }
            }}>
            <Center flexDirection="row">
                <Flex direction="column">
                    <Image
                        w="55px"
                        h="50px"
                        src={batteryLogo}
                    />
                    <Center alignItems="baseline">
                        <Text fontSize="24px" fontWeight="bold">{remainingAmount}</Text>
                        <Text fontSize="14px">%</Text>
                    </Center>
                </Flex>
                <Flex w="155px" direction="column" marginLeft="20px">
                    <Text fontSize="14px" fontWeight="bold">{unitID}</Text>
                </Flex>
            </Center>
            <Text
                fontSize="14px"
                fontWeight="bold"
                //color="111111"
            >
                製造情報未登録
            </Text>
        </Center>
    );
}

export default UnproductedBatteryCard;