import React from 'react';
import { useHistory } from 'react-router-dom';
import {
    Center,
    Text,
    Input,
    Flex,
    Box,
} from '@chakra-ui/react';
import axios from "axios"

import {apiURL,Contracts,ContractsJson} from "../../modules/interfaces"

import HeaderSubBack from "../../components/HeaderSubBack"
import BackButton from "../../components/buttons/BackButton";
import SubmitButton from "../../components/buttons/SubmitButton";

interface ContractRegistrationReviewPageProps {
    location: {
        state:{
            company: string,
            officeName: string,
            account_id: number,
            department_id: number,
            contractData: {
                contractName: string,
                contractStartDate: string,
                contractEndDate: string,
                contractType: string,
            },
        }
    }
}

const ContractRegistrationReviewPage = (props: ContractRegistrationReviewPageProps) => {
    const { location } = props;
    const company = location.state.company;
    const officeName = location.state.officeName;
    const account_id = location.state.account_id;
    const department_id = location.state.department_id;
    console.log(department_id)

    const contractName = location.state.contractData.contractName;
    const contractStartDate = location.state.contractData.contractStartDate;
    const contractEndDate = location.state.contractData.contractEndDate;
    const contractType = location.state.contractData.contractType;

    const history = useHistory();

    const defaultContracts:Contracts[] = [];
    const [contracts, setContracts]: [Contracts[], (contracts: Contracts[]) => void] = React.useState(defaultContracts);

    React.useEffect(() => {
        axios
            .get<Contracts[]>(apiURL+"api/v1/contracts/", {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setContracts(response.data);
            });
        }, []);
        console.log('contracts data should be fetched at this point');
        console.log(contracts)
    
    let addContractData:ContractsJson;
    
    let len=0
    let contract_id = 0

    if (contracts!=null){
        len = contracts.length
    }

    if (len!=0){
        let last_id = contracts[len-1].contract_id
        contract_id = last_id + 1
    }else{
        contract_id = 1
    }

    addContractData={
        contract_id: String(contract_id),
        department_id: String(department_id),
        contract_name: contractName,
        contract_type: contractType,
        execution_date: contractStartDate,
        expiration_date: contractEndDate,
    }
    console.log(addContractData)


    return (
        <div>
        <HeaderSubBack
          headerText="登録内容の確認"
          onClick= {() => {
            history.push({
                pathname: `/contract-view`,
                state: {
                    company: company,
                    officeName: officeName,
                    account_id: account_id,
                    department_id: department_id,
                    contractData: {
                        contractName: contractName,
                        contractStartDate: contractStartDate,
                        contractEndDate: contractEndDate,
                        contractType: contractType,
                    },
                }
            })
        }}
        />
        <Center padding = "15px" flexDirection="column">
            <Text
                w="260px"
                fontSize="16px"
                fontWeight="bold"
                margin="10px"
                marginBottom="30px"
            >
                以下の登録内容でよろしいですか？
            </Text>
            <Text
                w="260px"
                fontSize="16px"
                fontWeight="bold"
                marginBottom="5px"
                color="#504E4E"
            >
                企業名
            </Text>
            <Text
                w="260px"
                fontSize="16px"
                fontWeight="bold"
                marginBottom="15px"
            >
                {company}
            </Text>
            <Text
                w="260px"
                fontSize="16px"
                fontWeight="bold"
                marginBottom="5px"
                color="#504E4E"
            >
                事業所名
            </Text>
            <Text
                w="260px"
                fontSize="16px"
                fontWeight="bold"
                marginBottom="20px"
            >
                {officeName}
            </Text>
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                marginBottom="5px"
                color="#504E4E"
            >
                契約名
            </Text>
            <Input
                w="260px"
                marginBottom="15px"
                bgColor="darkgray"
                value={contractName}
                isReadOnly
            />
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                marginBottom="5px"
                color="#504E4E"
            >
                契約開始年月
            </Text>
            <Input
                w="260px"
                marginBottom="15px"
                bgColor="darkgray"
                value={contractStartDate}
                isReadOnly
            />
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                marginBottom="5px"
                color="#504E4E"
            >
                契約終了年月
            </Text>
            <Input
                w="260px"
                marginBottom="15px"
                bgColor="darkgray"
                value={contractEndDate}
                isReadOnly
            />
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                marginBottom="5px"
                color="#504E4E"
            >
                契約タイプ
            </Text>
            <Input
                w="260px"
                marginBottom="30px"
                bgColor="darkgray"
                value={contractType}
                isReadOnly
            />
            <Flex
                w="269px"
                direction="row"
                marginLeft="-5px"
                justifyContent="space-between"
            >
                {/*
                <BackButton
                    onClick= {() => {
                        history.push({
                            pathname: `/contract-view`,
                            state: {
                                company: company,
                                officeName: officeName,
                                account_id: account_id,
                                department_id: department_id,
                                contractData: {
                                    contractName: contractName,
                                    contractStartDate: contractStartDate,
                                    contractEndDate: contractEndDate,
                                    contractType: contractType,
                                },
                            }
                        })
                    }}
                />
                */}
                <SubmitButton
                    onClick= {() => {
                        //TODO: DBに登録
                        console.log("json data")
                        console.log(addContractData)

                        axios
                            .post(apiURL+"api/v1/contract/post/", addContractData,
                            {
                                headers: {
                                    "Content-Type": "applicaton/json",
                                    //"Content-Type": "text/plain",
                                },
                            }
                            )
                            .then(function (response) {
                                console.log(response.data)
                                })
                            .catch(function (error) {
                                console.log(error)
                                })
                            .then(function () {
                                console.log ("*** 終了 ***")
                                })
  
                            console.log('add new contract to DB');


                        history.push({
                            pathname: `/contract-complete`,
                            state: {
                                company: company,
                                officeName: officeName,
                                account_id: account_id,
                                department_id: department_id,

                                contractName: contractName,
                                //TODO:contractIDの置き換え
                                contract_id: contract_id,
                            }
                        })
                    }}
                />
            </Flex>
        </Center>
        </div>
    );
}

export default ContractRegistrationReviewPage;