import React from 'react';
import { useHistory } from 'react-router-dom';
import {Center, Box, Text, Divider} from "@chakra-ui/react";
import axios from 'axios';

import {apiURL,BatteryOption,BatteryOptionJson,Charger,ChargerJson,Units,Customers} from "../../modules/interfaces"
import SearchBar from "../../components/SearchBar"
import AddCustomerButton from "../../components/buttons/AddButton";
import CompanyCard from "../../components/cards/CompanyCard";
import StatusBar from "../../components/StatusBar";
import Header from "../../components/Header";

const ContractRegistrationHomePage = () => {

    const defaultCustomers:Customers[] = [];
    const [customers, setCustomers]: [Customers[], (customers: Customers[]) => void] = React.useState(defaultCustomers);

    React.useEffect(() => {
        axios
            .get<Customers[]>(apiURL+"api/v1/customers/", {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setCustomers(response.data);
            });
        }, []);
        console.log('customers data should be fetched at this point');
        console.log(customers)
    
    const iconURL = ""

    const history = useHistory();

    return (
        <div>
        <Header
          drawerItems={[
            {
              title: "ホーム",
              path: "/",
            },
            {
              title: "バッテリー",
              path: "/units",
            },
            {
              title: "お客様",
              path: "/customer-view",
            },
          ]}
          //iconName={iconname}
        />
        <Center　padding="15px" flexDirection="column">
            <Box>
                <Text margin="15px" fontSize="22px" fontWeight="bold">新規契約登録先の選択/登録</Text>
                <Text margin="15px" fontSize="12px">
                    {`事業所・現場情報の登録を行う顧客を選択して下さい。\nもしくは以下のボタンより新しく顧客登録をしてください。`}
                </Text>
            </Box>
            <StatusBar activeStep={0}/>
            {/*<Divider/>*/}
            <Box margin="10px" marginTop="-10px">
                <AddCustomerButton
                    label="新規顧客登録"
                    onClick={()=>{
                    history.push(`/registration-view`)
                }}
            />
            </Box>
            {/*
            <Box marginBottom="10px">
                <SearchBar onChange={(value)=>{
                    // TODO:フィルタリングの処理を入れる
                    }}
                />
            </Box>
            */}
            <Box>
                {customers.map((value) => {
                    let account_id_str = String(value.account_id)
                    return (
                        <Box margin="20px">
                            <CompanyCard
                                iconURL={iconURL}
                                companyName={value.corporation_name}
                                customerID={account_id_str}
                                jobType={value.sector}
                                //jobType=""
                                onClick={() => {
                                    history.push
                                    ({
                                        pathname: `/customer-department`,
                                        state: {
                                            account_id: value.account_id,
                                            company_name: value.corporation_name,
                                        },
                                    });
                                }}
                            />
                        </Box>
                    );
                })}
            </Box>
        </Center>
        </div>
    );
}

export default ContractRegistrationHomePage;