import React, { useState } from "react";
import { useForm } from "react-hook-form";
import { useHistory } from "react-router-dom";
import {
  Center,
  Box,
  Text,
  Input,
  RadioGroup,
  Radio,
  Spacer,
  Flex,
  Checkbox,
} from "@chakra-ui/react";
import axios from "axios";

import {
  apiURL,
  BatteryOption,
  Charger,
  ChargerJson,
  Units,
} from "../../modules/interfaces";
import {
  inputValidation,
  numValidation,
} from "../../utils/validations";
import BackButton from "../../components/buttons/BackButton";
import SubmitButton from "../../components/buttons/SubmitButton";
import HeaderSubBack from "../../components/HeaderSubBack";
import { truncate } from "node:fs";

import TextField from '@material-ui/core/TextField';
import { RiExternalLinkFill } from "react-icons/ri";

import AWS from "aws-sdk";
import S3 from "aws-sdk/clients/s3";

type FormInputs = {
  chargeName: string;
  provideMethod: string;
  productNumber: string;
  provideElse: string;
  powerAmp: string;
  isNeedFrame: boolean;
  powsupToChargerLength: string;
  chargerToForkliftLength: string;
  isBatteryTemp: boolean;
  isBatteryDust: boolean;
  isBatteryElse: boolean;
  batteryEtc: string;
  isNeedWiringWork: boolean;
  comment: string;
};

interface DepartmentFieldRegistrationChargeViewPageProps {
  location: {
    state: {
      company: string;
      officeName: string;
      account_id: number;
      department_id: number;
      is_update: boolean;
    };
  };
}

const DepartmentFieldRegistrationChargeViewPage = (
  props: DepartmentFieldRegistrationChargeViewPageProps
) => {
  const { location } = props;
  const company = location.state.company;
  const officeName = location.state.officeName;
  const account_id = location.state.account_id;
  const department_id = location.state.department_id;
  const is_update = location.state.is_update;

  let provideMethod = "";
  let isBatteryTemp = false
  let isBatteryDust = false
  let isBatteryElse = false
  let isNeedWiringWork = false

  const [provideMethodState, setProvideMethodState] = useState("");
  //const [provideMethodValue, setProvideMethodValue] = useState(provideMethod)
  const [isBatteryTempValue, setIsBatteryTempValue] = useState(isBatteryTemp);
  const [isBatteryDustValue, setIsBatteryDustValue] = useState(isBatteryDust);
  const [isBatteryElseValue, setIsBatteryElseValue] = useState(isBatteryElse);
  const [isNeedWiringWorkValue, setIsNeedWiringWorkValue] = useState(isNeedWiringWork);

  const [imageFile, setImageFile] = useState<File | null>(null);

  AWS.config.update({
    accessKeyId: process.env.AWS_ACCESS_KEY,
    secretAccessKey: process.env.AWS_SECRET_KEY,
  });

  const {
    register,
    formState: { errors, },
    handleSubmit,
  } = useForm<FormInputs>();

  const onSubmit = (data: FormInputs) => {
    let json: ChargerJson;
    let stand_json = "noNeed";
    if (data.isNeedFrame) {
      stand_json = "need";
    }

    let charger_setting_help="noNeed";
    if(data.isNeedWiringWork){
      charger_setting_help="need";
    }

    let charger_environment_json = "room";
    if(data.isBatteryDust){
      charger_environment_json = "dust"
    }else if(data.isBatteryDust){
      charger_environment_json = "else"
    }

    if (imageFile) {
      console.log("files3")
      console.log(imageFile.name)

      if (!imageFile.name.match(/^[0-9a-zA-Z\.\_\-]*$/)) {
        console.log("ERROR: ファイル名が正しくありません");

        return;
      }
      // TODO: identityIDの設定
      const accessKeyId = process.env.REACT_APP_AWS_ACCESS_KEY;
      const secretAccessKey = process.env.REACT_APP_AWS_SECRET_KEY; // IAMユーザのシークレットアクセスキー。アクセスキーを作ったときだけ見れるやつです。
      const bucketName = process.env.REACT_APP_BUCKET; // 保存先のバケット名
      console.log("bucket name: "+bucketName)
      
      const bucket = new S3({
        accessKeyId: accessKeyId,
        secretAccessKey: secretAccessKey,
        region: "ap-northeast-1",
      });
      const param: S3.Types.PutObjectRequest = {
        Bucket: bucketName!,
        Key: "biz/"+imageFile.name, // ファイル絶対パス
        Body: imageFile, // ファイルの内容
        ACL: "public-read", // インターネットから誰でもダウンロードできるように
        ContentType: imageFile.type,
      };
      bucket.upload(param, (err: Error, data: S3.ManagedUpload.SendData) => {
        if (err) {
          console.error(err);
          return;
        } else {
          console.log("Successfully uploaded file.", data);
        }
      });
    }

    json = {
      charger_id: String(charger_last_id + 1),
      department_id: String(department_id),
      contract_id: "",//ここではnullであることが必須
      update_date: "",//更新日:一旦null
      info_type: "field",//ここは必ずfield
      charger_name: data.chargeName,
      charger_environment: charger_environment_json||"",//data.isBatteryDust||"",//isbatterytemp
      charger_environment_else: data.batteryEtc||"",//isbatterydust
      how2supply: data.provideMethod,
      supply_plug: data.productNumber||"",
      supply_else: data.provideElse||"",
      power_supply_ampere: data.powerAmp,
      stand: stand_json,
      power_supply2charger_cable_length: data.powsupToChargerLength,
      charger2forklift_cable_length: data.chargerToForkliftLength,
      charger_setting_help: charger_setting_help,
      comment: data.comment||"",
      pic_charger_stand: imageFile?.name || "",
      //pic_power_supply
      //pic_supply_plug
    };
    console.log(json);
    
    axios
      .post(apiURL + "api/v1/charger/post/", json, {
        headers: {
          "Content-Type": "applicaton/json",
          //"Content-Type": "text/plain",
        },
      })
      .then(function (response) {
        console.log(response.data);
      })
      .catch(function (error) {
        console.log(error);
      })
      .then(function () {
        console.log("*** 終了 ***");
      });

    console.log("add new chargers to DB");

    history.push({
      pathname: `/department-registration`,
      state: {
        company: company,
        officeName: officeName,
        account_id: account_id,
        department_id: department_id,
        is_update: true,
      },
    });
  };

  //charger 最新idを入手
  //chargerデータの取得
  const defaultChargers: Charger[] = [];
  const [chargers, setChargers]: [
    Charger[],
    (chargers: Charger[]) => void
  ] = React.useState(defaultChargers);

  React.useEffect(() => {
    axios
      .get<Charger[]>(apiURL + "api/v1/chargers/", {
        headers: {
          "Content-Type": "application/json",
        },
      })
      .then((response) => {
        setChargers(response.data);
      });
  }, []);

  console.log(chargers);

  console.log("charger's last_id is");
  let charger_last_id = 0;
  if (chargers!=null && chargers.length != 0) {
    let len = chargers.length;
    charger_last_id = chargers[len - 1].charger_id;
  }
  console.log(charger_last_id);

  const history = useHistory();
  return (
    <div>
        <HeaderSubBack
          headerText="充電器情報の登録"
          onClick={() => {
            history.push({
              pathname: "/department-registration",
              state: {
                company: company,
                officeName: officeName,
                account_id: account_id,
                department_id: department_id,
                is_update: is_update,
            },
            });
          }}
        />
    <Center flexDirection="column" padding="15px">
      <form onSubmit={handleSubmit(onSubmit)}>
        <Box margin ="20px" marginTop="20px">
          <Text w="260px" fontSize="18px" fontWeight="bold">
            充電器情報を入力してください
          </Text>
        </Box>
        <Box margin ="20px">
        <Flex
          direction="row"
          w="260px"
          fontSize="11px"
          fontWeight="bold"
          color="#504E4E"
          marginBottom="10px"
        >
          <Text>充電器登録名</Text>
          {errors.chargeName && (
            <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
              必須
            </Text>
          )}
        </Flex>
        <Input
          w="260px"
          placeholder="例) 充電器A"
          marginBottom="20px"
          isInvalid={errors.chargeName ? true : false}
          {...register("chargeName", { required: true,
            pattern: {
              value: inputValidation,
              message: "スペースのみは入力できません",
            },
          })}
        />
        <Flex
          direction="row"
          w="260px"
          fontSize="11px"
          fontWeight="bold"
          color="#504E4E"
          marginBottom="10px"
        >
          <Text>充電器電気供給方法</Text>
          {errors.provideMethod && (
            <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
              必須
            </Text>
          )}
        </Flex>
        <Flex direction="row" marginBottom="10px">
          <RadioGroup
            isInvalid={errors.provideMethod ? true : false}
            {...register("provideMethod", { required: true })}
            onChange={setProvideMethodState} value={provideMethodState}
          >
            <Flex
              w="260px"
              justifyContent="space-between"
              direction="row"
              verticalAlign="center"
            >
              <Radio
                /*
                value={provideMethodValue}
                onChange={(e) => {
                  setProvideMethodValue(e.target.value);
                }}
                */
                value="switchboard"
                >
                <Text fontSize="11px" fontWeight="bold">
                  配線盤
                </Text>
              </Radio>
              <Radio value="plug">
                <Text fontSize="11px" fontWeight="bold">
                  プラグ
                </Text>
              </Radio>
              <Radio value="else">
                <Text fontSize="11px" fontWeight="bold">
                  その他
                </Text>
              </Radio>
            </Flex>
          </RadioGroup>
        </Flex>
        {provideMethodState=="plug" &&(
          <Input
            w="260px"
            placeholder="品番"
            marginBottom="20px"
            {...register("productNumber")}
          />
        )}
        {provideMethodState=="else" &&(
          <Input
            w="260px"
            placeholder="その他（品番）"
            marginBottom="20px"
            {...register("provideElse")}
          />
        )}

        <Flex direction="row">
          <Text
            w="260px"
            fontSize="11px"
            fontWeight="bold"
            color="#504E4E"
            marginBottom="10px"
          >
            電源アンペア数
          </Text>
          {errors.powerAmp && (
            <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
              必須
            </Text>
          )}
        </Flex>
        <Flex w="260px" direction="row" marginBottom="20px">
          <Input
            w="50px"
            {...register("powerAmp", { required: true,
              pattern: {
                value: numValidation,
                message: "0以上の整数で入力してください",
              },
            })}
            isInvalid={errors.powerAmp ? true : false}
          />
          <Flex direction="column">
            <Spacer />
            <Text fontSize="11px" fontWeight="bold" color="#504E4E" marginLeft="5px">
              A
            </Text>
          </Flex>
          {errors.powerAmp && (
              <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                {errors.powerAmp.message}
              </Text>
            )}
        </Flex>
        <Text
          w="260px"
          fontSize="11px"
          fontWeight="bold"
          color="#504E4E"
          marginBottom="10px"
        >
          架台
        </Text>
        <Checkbox
          w="260px"
          marginLeft="20px"
          marginBottom="20px"
          {...register("isNeedFrame")}
        >
          <Text fontSize="11px" fontWeight="bold">
            必要
          </Text>
        </Checkbox>
        <Flex direction="row">
          <Text
            w="260px"
            fontSize="11px"
            fontWeight="bold"
            color="#504E4E"
            marginBottom="10px"
          >
            電源供給から充電器設置場所までのケーブル長さ
          </Text>
          {errors.powsupToChargerLength && (
            <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
              必須
            </Text>
          )}
        </Flex>
        <Flex w="260px" direction="row" marginBottom="20px">
          <Input
            w="70px"
            {...register("powsupToChargerLength", { required: true,
              pattern: {
                value: numValidation,
                message: "0以上の整数で入力してください",
              },
            })}
            isInvalid={errors.powsupToChargerLength ? true : false}
          />
          <Flex direction="column">
            <Spacer />
            <Text fontSize="11px" fontWeight="bold" color="#504E4E" marginLeft="5px">
              m
            </Text>
          </Flex>
          {errors.powsupToChargerLength && (
              <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                {errors.powsupToChargerLength.message}
              </Text>
            )}
        </Flex>
        <Flex
          direction="row"
          w="260px"
          fontSize="11px"
          fontWeight="bold"
          color="#504E4E"
          marginBottom="10px"
        >
          <Text>充電器からフォークリフトまでのケーブル長さ</Text>
          {errors.chargerToForkliftLength && (
            <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
              必須
            </Text>
          )}
        </Flex>
        <Flex w="260px" direction="row" marginBottom="20px">
          <Input
            w="70px"
            {...register("chargerToForkliftLength", { required: true,
              pattern: {
                value: numValidation,
                message: "0以上の整数で入力してください",
              },
            })}
            isInvalid={errors.chargerToForkliftLength ? true : false}
          />
          <Flex direction="column">
            <Spacer />
            <Text fontSize="11px" fontWeight="bold" color="#504E4E" marginLeft="5px">
              m
            </Text>
          </Flex>
          {errors.chargerToForkliftLength && (
              <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                {errors.chargerToForkliftLength.message}
              </Text>
            )}
        </Flex>
        <Flex
          direction="row"
          w="260px"
          fontSize="11px"
          fontWeight="bold"
          color="#504E4E"
          marginBottom="5px"
        >
          <Text>バッテリー充電環境</Text>
          {/*
          {(isBatteryTempValue ||
            isBatteryDustValue)==false && (
              <Text fontSize="9px" color="#BB2C2C" marginLeft="5px">
                必須
              </Text>
            )}
            */}
        </Flex>
        <Flex w="260px" marginBottom="20px">
          <Checkbox 
            {...register("isBatteryTemp")}
            isInvalid={errors.isBatteryTemp ? true : false}
            checked={isBatteryTempValue}
            onChange={(e) => {
              setIsBatteryTempValue(e.target.checked);
            }}
            >
            <Text fontSize="11px" fontWeight="bold">
              常温
            </Text>
          </Checkbox>
          <Checkbox marginLeft="40px" 
            {...register("isBatteryDust")}
            isInvalid={errors.isBatteryDust ? true : false}
            checked={isBatteryDustValue}
            onChange={(e) => {
              setIsBatteryDustValue(e.target.checked);
            }}
            >
            <Text fontSize="11px" fontWeight="bold">
              粉塵あり
            </Text>
          </Checkbox>
          <Checkbox marginLeft="40px" 
            {...register("isBatteryElse")}
            isInvalid={errors.isBatteryElse ? true : false}
            checked={isBatteryElseValue}
            onChange={(e) => {
              setIsBatteryElseValue(e.target.checked);
            }}
            >
            <Text fontSize="11px" fontWeight="bold">
              その他
            </Text>
          </Checkbox>
        </Flex>
        {isBatteryElseValue &&(
        <Input
          w="260px"
          placeholder="備考"
          marginBottom="10px"
          {...register("batteryEtc", { required: true })}
        />
        )}

        <Text
          w="260px"
          fontSize="11px"
          fontWeight="bold"
          color="#504E4E"
          marginBottom="15px"
        >
          充電器配線設備作業
        </Text>
        <Checkbox
          w="260px"
          marginBottom="25px"
          {...register("isNeedWiringWork")}
          checked={isNeedWiringWorkValue}
          onChange={(e) => {
            setIsNeedWiringWorkValue(e.target.checked);
          }}
        >
          <Text fontSize="11px" fontWeight="bold">
            必要
          </Text>
        </Checkbox>

        <Flex direction="row">
          <Text
            w="260px"
            fontSize="11px"
            fontWeight="bold"
            color="#504E4E"
            marginBottom="10px"
          >
            特記事項
          </Text>
        </Flex>
        <Flex w="260px" direction="row" marginBottom="40px">
          {/*<Input w="269px" h="28px" {...register("comment")} />*/}
          <TextField
            //id="outlined-multiline-static"
            //label="Multiline"
            multiline
            rows={4}
            defaultValue=""
            //variant="outlined"
            {...register("comment")}
          />
        </Flex>
        
        <Text
          w="260px"
          fontSize="11px"
          fontWeight="bold"
          color="#504E4E"
          marginBottom="15px"
        >
          充電器設置場所の写真
        </Text>
        <Box marginLeft="0px" marginBottom="50px" w="260px">
              <Input
                type="file"
                onChange={(e) => {
                  //console.log("files1")
                  //console.log(e.target.files)
                  if (e.target.files) {
                    setImageFile(e.target.files[0]);
                  }
                }}
              />
            </Box>

        </Box> 
        <Flex w="260px" direction="row" justifyContent="space-between" margin="20px">
          {/*
          <BackButton
            onClick={() => {
              history.push({
                pathname: `/department-registration`,
                state: {
                  company: company,
                  officeName: officeName,
                  account_id: account_id,
                  department_id: department_id,
                  is_update: is_update,
                },
              });
            }}
          />
          */}
          <SubmitButton type="submit" />
        </Flex>
      </form>
    </Center>
    </div>
  );
};

export default DepartmentFieldRegistrationChargeViewPage;
