import React from 'react';
import { useHistory } from 'react-router-dom';
import {
    Center,
    Text,
    Flex,
    Box,
    Spacer,
} from '@chakra-ui/react';
import axios from 'axios';

import HeaderSubBack from 'components/HeaderSubBack';
import {apiURL,BatteryOption,BatteryOptionJson,Charger,ChargerJson,Units} from "../../modules/interfaces"
import BackButton from "../../components/buttons/BackButton";
import ChargerCard from "../../components/cards/ChargerCard";
import { getValueTransition } from 'framer-motion/types/animation/utils/transitions';

interface CarDetailViewPageProps {
    location: {
        state:{
            company: string,
            officeName: string,
            contractName: string,
            contract_id: string,
            account_id: number,
            department_id: number,
            battery_option_id: number [],
            charger_id: number [],
            charger_name: string [],
            option_name: string [],
            option_num: number [],
            charger_num: number [],
            carName?: string,
            chargerName?: string,
        }
    }
}

const CarDetailViewPage = (props: CarDetailViewPageProps) => {
    const { location } = props;
    const company = location.state.company;
    const officeName = location.state.officeName;
    const contractName = location.state.contractName;
    const contract_id = location.state.contract_id;
    const account_id = location.state.account_id;
    const department_id = location.state.department_id;
    const carName = location.state.carName || "";
    const chargerName = location.state.chargerName || "";
    const battery_option_id = location.state.battery_option_id || [];
    const charger_id = location.state.charger_id || [];
    const charger_name = location.state.charger_name || [];
    const option_name = location.state.option_name || [];
    const option_num = location.state.option_num||[];
    const charger_num = location.state.charger_num || [];

    const history = useHistory();

    //充電器データの取得
    //charger optionデータの取得
    const defaultChargers:Charger[] = [];
    const [chargers, setChargers]: [Charger[], (chargers: Charger[]) => void] = React.useState(defaultChargers);

    React.useEffect(() => {
        axios
            .get<Charger[]>(apiURL+"api/v1/customercharger/?department_id=" + department_id, {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setChargers(response.data);
            });
        }, []);
        console.log('chargers data should be fetched at this point. department id = '+ String(department_id));
        console.log(chargers)

    //info_typeがfieldかつcontract_idが0のもののみを取得
    let fieldChargers:Charger[] = [];
    if(chargers.length!=0){
        for (let i=0;i<chargers.length;i++){
            if(chargers[i].info_type=="field" && chargers[i].contract_id.Int32==0){
                fieldChargers[i]=chargers[i]
            }
        }
    }

    return (
        <div>
        <HeaderSubBack
            headerText = "充電器情報の選択"
            onClick={() => {
                history.push({
                    pathname: `/battery-registration`,
                    state: {
                        company: company,
                        officeName: officeName,
                        contractName: contractName,
                        contract_id: contract_id,
                        account_id: account_id,
                        department_id: department_id,
                        battery_option_id: battery_option_id,
                        charger_id: charger_id,
                        option_name: option_name,
                        charger_name: charger_name,
                        option_num: option_num,
                        charger_num: charger_num,
                        //carName: carName,
                        //chargerName: chargerName,
                    }
                });
            }}
        />
        <Center flexDirection="column">
            <Text
                w="260px"
                fontSize="18px"
                fontWeight="bold"
                margin="30px"
            >
                充電器製造登録を行う充電器情報を選択してください
            </Text>
            {fieldChargers.map((value) => {
                let id = value.charger_id
                let name = value.charger_name
                let frame = false
                if (value.stand=="need"){
                    frame = true
                }
                return(
                    <Box margin="10px">
                        <ChargerCard
                            name={name}
                            provideMethod={value.how2supply}
                            powerAmp={String(value.power_supply_ampere.Int32)}
                            isNeedFrame={frame}
                            onClick= {() => {
                                //charger_id.push(id)
                                //charger_name.push(name)
                                history.push({
                                    pathname: `/charger-detail2`,
                                    state: {
                                        company: company,
                                        officeName: officeName,
                                        contractName: contractName,
                                        contract_id: contract_id,
                                        account_id: account_id,
                                        department_id: department_id,
                                        battery_option_id: battery_option_id,
                                        charger_id: charger_id,
                                        charger_name: charger_name,
                                        option_name: option_name,
                                        option_num: option_num,
                                        charger_num: charger_num,
                                        target_id: id,
                                        target_name: name,
                                        //carName: "車体A",
                                        //chargerName: "充電器A",
                                    }
                                });
                            }}
                        />
                    </Box>
                );
            })}
            {/*
            <Flex w="260px" marginTop="50px" direction="row">
                <BackButton
                    onClick={() => {
                        history.push({
                            pathname: `/battery-registration`,
                            state: {
                                company: company,
                                officeName: officeName,
                                contractName: contractName,
                                contract_id: contract_id,
                                account_id: account_id,
                                department_id: department_id,
                                battery_option_id: battery_option_id,
                                charger_id: charger_id,
                                charger_name: charger_name,
                                //carName: carName,
                                //chargerName: chargerName,
                            }
                        });
                    }}
                />
                <Spacer/>
            </Flex>
                */}
        </Center>
        </div>
    );
}

export default CarDetailViewPage;