import React, {useState} from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Box, Text, Flex, Button, useUpdateEffect } from '@chakra-ui/react';
import {apiURL,BatteryOption,Charger,Units} from "../../modules/interfaces"
import axios from 'axios';

import Header from "../../components/Header";
import AddButton from "../../components/buttons/AddButton";
import UpdateButton from "../../components/buttons/UpdateButton";
import BatteryOptionCard from "../../components/cards/BatteryOptionCard";
import ChargerOptionCard from "../../components/cards/ChargerOptionCard";
import { StringLiteralLike } from 'typescript';
import StatusBar from "../../components/StatusBar"

interface DepartmentFieldRegistrationViewPageProps {
    location: {
        state: {
            company: string,
            officeName: string,
            account_id: number,
            department_id: number,
            is_update: boolean,
        }
        //addListener: EventListener
    }
}

const DepartmentFieldRegistrationViewPage = (props: DepartmentFieldRegistrationViewPageProps) => {
    const { location } = props;
    const company = location.state.company;
    const officeName = location.state.officeName;
    const account_id = location.state.account_id;
    const department_id = location.state.department_id;
    const is_update = location.state.is_update||false;

    //TODO:このStateを更新する挙動を入れる
    const [update, setUpdate] = useState<number>(0);
    let cnt:number = 0
    const [isSubmissionCompletedState, setIsSubmissionCompletedState] = useState(true);

    const history = useHistory();

    //以下department_id & account_idを元に
    //battery_optionデータの取得
    const defaultOptions:BatteryOption[] = [];
    const [options, setOptions]: [BatteryOption[], (options: BatteryOption[]) => void] = React.useState(defaultOptions);

    React.useEffect(() => {
        axios
            .get<BatteryOption[]>(apiURL+"api/v1/customerbatteryoption/?department_id=" + department_id, {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setOptions(response.data);
            });
        }, []);
        console.log('battery options data should be fetched at this point');
        console.log(options)

    //info_typeがfieldかつcontract_id==0のもののみを取得　もしあれば
    let fieldOptions:BatteryOption[] = [];
    if(options!=null){
        for (let i=0;i<options.length;i++){
            if(options[i].info_type=="field" && options[i].contract_id.Int32==0){
                fieldOptions[i]=options[i]
            }
        }
    }

    //のちのpage
    //契約登録時はこれを貼り付ける->contract_idを付与
    //製造情報登録時はinfo_typeを変更したものをあらたにDB create->options_idはnullでok、前のcontract_id,infotypeを戻す

    //充電器データの取得
    //charger optionデータの取得
    const defaultChargers:Charger[] = [];
    const [chargers, setChargers]: [Charger[], (chargers: Charger[]) => void] = React.useState(defaultChargers);

    React.useEffect(() => {
        axios
            .get<Charger[]>(apiURL+"api/v1/customercharger/?department_id=" + department_id, {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setChargers(response.data);
            });
        }, []);
        console.log('chargers data should be fetched at this point');
        console.log(chargers)
        //console.log(chk)

    //info_typeがfieldかつcontract_id=0のもののみを取得
    let fieldChargers:Charger[] = [];
    if(chargers!=null){
        for (let i=0;i<chargers.length;i++){
            if(chargers[i].info_type=="field" && chargers[i].contract_id.Int32==0){
                fieldChargers[i]=chargers[i]
            }
        }
    }

    return (
        <div>
        <Header
          drawerItems={[
            {
              title: "ホーム",
              path: "/",
            },
            {
              title: "バッテリー",
              path: "/units",
            },
            {
              title: "お客様",
              path: "/customer-view",
            },
          ]}
          //iconName={iconname}
        />
        <Center padding="15px" flexDirection="column">
            <Box>
            <Text
                fontSize="22px"
                fontWeight="bold"
                margin="15px"
                w="250px"
            >
                現場情報の登録
            </Text>
            <Text margin="15px" fontSize="12px">
                {`導入前確認シートに基づき、車体情報および\n充電器情報をそれぞれ登録してください。`}
            </Text>
            </Box>
            <StatusBar activeStep={1}/>
            <Box marginTop="-10px" marginBottom="10px">
                <UpdateButton
                    onClick={() => {
                        cnt++;
                        setUpdate(cnt);
                        console.log(cnt,update)

                        axios
                        .get<BatteryOption[]>(apiURL+"api/v1/customerbatteryoption/?department_id=" + department_id, {
                            headers: {
                                "Content-Type": "application/json"
                            },
                        })
                        .then(response => {
                            setOptions(response.data);
                        });
                        
                        axios
                        .get<Charger[]>(apiURL+"api/v1/customercharger/?department_id=" + department_id, {
                            headers: {
                                "Content-Type": "application/json"
                            },
                        })
                        .then(response => {
                            setChargers(response.data);
                        });
                    }}
                ></UpdateButton>
            </Box>
            <Box margin="20px">
                <AddButton
                    label="バッテリーを載せる車体を追加"
                    onClick={() => {
                        history.push({
                            pathname: `department-registration1`,
                            state: {
                                company: company,
                                officeName: officeName,
                                account_id: account_id,
                                department_id: department_id,
                                is_update: false,
                            }
                        });
                    }}
                />
            </Box>
            {fieldOptions.map((value) => {
                return(
                    <Box>
                        <BatteryOptionCard
                            batteryOptionID={String(value.battery_option_id)}
                            batteryOptionName={String(value.option_name)}
                            carType={value.forklift}
                            type = {value.type}
                            serialNumber = {value.serial_number}
                        />
                    </Box>
                );
            })}
            <Box margin="20px">
                <AddButton
                    label="充電器情報を追加"
                    onClick={() => {
                        history.push({
                            pathname: `department-charge`,
                            state: {
                                company: company,
                                officeName: officeName,
                                account_id: account_id,
                                department_id: department_id,
                                is_update: false,
                            }
                        });
                    }}
                />
            </Box>
            {fieldChargers.map((value) => {
                return(
                    <Box>
                        <ChargerOptionCard
                            chargerOptionID={String(value.charger_id)}
                            chargerOptionName={value.charger_name}
                            how2Supply={value.how2supply}
                            powerSupplyAmpere={value.power_supply_ampere.Int32}
                            stand={value.stand}
                        />
                    </Box>
                );
            })}
            {/*
            <Text margin="20px">
                現場情報を登録してください
            </Text>
            */}
            {
                isSubmissionCompletedState &&
                <Flex direction="column" alignItems="center">
                    <Button
                        w="196px"
                        marginTop="20px"
                        marginBottom="20px"
                        color="#504E4E"
                        bgColor="white"
                        border="2px"
                        borderColor="#504E4E"
                        onClick={() => {
                            if(fieldChargers.length==0||fieldOptions.length==0){
                                console.log("error")
                            }
                            else{
                            history.push({
                                pathname: `/contract-view`,
                                state: {
                                    company: company,
                                    officeName: officeName,
                                    account_id: account_id,
                                    department_id: department_id,
                                    is_update: is_update,
                                },
                            });
                            }
                        }}
                    >
                        続けて契約を登録する
                    </Button>
                    {/*
                    <Button
                        w="158px"
                        h="40px"
                        color="#504E4E"
                        bgColor="white"
                        border="2px"
                        borderColor="#504E4E"
                        onClick={() => {
                            history.push(`/customer-view`);
                        }}
                    >
                        顧客一覧へ
                    </Button>
                    */}
                </Flex>
            }
        </Center>
        </div>
    );
}

export default DepartmentFieldRegistrationViewPage;