import React from "react";
import {
    Flex,
    Text,
    Center,
    Image,
    Box
} from "@chakra-ui/react";

interface ManuChargerCardProps {
    chargerID: string,
    how2Supply: string,
    supplyAmpere: number,
    chargeEmv: string,
    onClick?: (value: string) => void;
}

const ManuChargerCard = (props: ManuChargerCardProps) => {
    const {chargerID, how2Supply, supplyAmpere, chargeEmv, onClick} = props;

    return (
        <Center
            w="267px"
            padding="10px"
            flexDirection="row"
            boxShadow="outline"
            style={{boxShadow: onClick !== undefined ?
                "2px 2px 4px rgba(0, 0, 0, 0.2)"
                : "0px 1px 0px rgba(0, 0, 0, 0.2)"
            }}
            onClick={() => {
                if (onClick !== undefined) {
                    onClick(chargerID)
                }
            }}>
            
            <Flex direction="column">
                <Box>
                    <Text fontSize="5px">充電器型番</Text>
                </Box>
                <Box px={4} marginBottom="10px">
                    <Text fontSize="15px" fontWeight="bold">{props.chargerID}</Text>
                </Box>
                <Box>
                    <Text fontSize="5px">充電器電気供給方法</Text>
                </Box>
                <Box px={4} marginBottom="10px">
                    <Text fontSize="15px" fontWeight="bold">{props.how2Supply}</Text>
                </Box>
                <Box>
                    <Text fontSize="5px">供給アンペア数</Text>
                </Box>
                <Box px={4} marginBottom="10px">
                    <Text fontSize="15px" fontWeight="bold">{props.supplyAmpere} A</Text>
                </Box>
                <Box>
                    <Text fontSize="5px">バッテリー充電環境</Text>
                </Box>
                <Box px={4} marginBottom="10px">
                    <Text fontSize="15px" fontWeight="bold">{props.chargeEmv}</Text>
                </Box>
            </Flex>
        </Center>
    )
}

export default ManuChargerCard;