    const [customer, setCustomer]: [Customer, (customer: Customer) => void] = React.useState(defaultCustomer);

    React.useEffect(() => {
        axios
            .get<Customer>(apiURL+"api/v1/customer/?account_id="+String(customer_id), {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setCustomer(response.data);
            });
        }, []);
        console.log('customer data should be fetched at this point');
        console.log(customer['customer'])

    //let icon_url = ""

    let customer_data = []
    customer_data.push(customer['customer'])

    //契約情報を取得
    const defaultContracts:Contracts[] = []//{contracts:[],units:[],customer:{account_id:0,corporation_name:"",sector:"",postal_code:"",address:"",name:"",position:"",mail:"",phone:""}}
    const [contracts, setContracts]: [Contracts[], (contracts: Contracts[]) => void] = React.useState(defaultContracts);
    
    //TODO contractsでdepartment_id=で取るべき apiを作成
    React.useEffect(() => {
        axios
            .get<Contracts[]>(apiURL+"api/v1/customercontract/?department_id="+String(department_id), {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setContracts(response.data);
            });
        }, []);
        console.log('customer contract data should be fetched at this point');
        console.log(contracts)
    
    
    let contract_data :Contracts[] = []
    if(contracts!=null && contracts.length!=0){
        contract_data = contracts
    }
    console.log(contract_data)
    

