import React from "react";
import {
    Flex,
    Text,
    Center,
    Button,
} from "@chakra-ui/react";
import { CloseIcon } from '@chakra-ui/icons'

interface ChargerMiniCardProps {
    name: string,
    chargernum: number,
    onRemoveHandler: () => void,
    noremove?: boolean,
    noheader?:boolean,
}

const ChargerMiniCard2 = (props: ChargerMiniCardProps) => {
    const {
        name,
        chargernum,
        onRemoveHandler,
        noremove,
        noheader,
    } = props;

    return (
        <Center
            w="267px"
            padding="10px"
            flexDirection="column"
            boxShadow="outline"
            style={{boxShadow: "2px 2px 4px rgba(0, 0, 0, 0.2)"}}
        >
            <Flex w="230px" direction="row" justifyContent="space-between">
                {noheader!=true && (
                <Text
                    color="#504E4E"
                    fontSize="11px"
                    fontWeight="bold"
                    marginBottom="5px"
                >
                    充電器製造　充電池情報
                </Text>
                )}
                {noremove!=true && (
                <CloseIcon
                    onClick={() => {
                        onRemoveHandler();
                    }}
                />
                )}
            </Flex>
            <Flex 
                w="230px" 
                direction="row"
                //justifyContent="space-between"
                >
                <Text
                    fontSize="16px"
                    fontWeight="bold"
                    marginBottom="5px"
                    marginRight="5px"
                >
                    {name}
                </Text>
                <Text>
                    × {chargernum}
                </Text>
            </Flex>
        </Center>
    );
}

export default ChargerMiniCard2;