import React from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Box, Text } from '@chakra-ui/react';
import axios from 'axios';

import {apiURL,BatteryOption,BatteryOptionJson,Charger,ChargerJson,Unit,Contracts,DateFormat,formatDate} from "../../modules/interfaces"

import BackButton from "../../components/buttons/BackButton";
import BatteryCard from "../../components/cards/BatteryCard";
import BatteryCard2 from "../../components/components2/BatteryCard";
import CompanyCard from "../../components/cards/CompanyCard";
import LongUnitDetailCard from "../../components/cards/LongUnitDetailCard";
import SearchBar from "../../components/SearchBar";
import { reduceEachTrailingCommentRange } from 'typescript';
import HeaderSubBack from 'components/HeaderSubBack';
import ContractCard from 'components/components2/ContractCard';
import { AndroidTwoTone } from '@material-ui/icons';

interface UnitDetailViewPageProps {
    location: {
        state: {
            contractID: number,
            unitID: string,
            customer_name: string,
            department_name: string,
        }
    }
}

const UnitDetailViewPage = (props: UnitDetailViewPageProps) => {
    const { location } = props;
    //const contractID = location.state.contractID;
    const unitID = location.state.unitID;
    const contract_id = location.state.contractID;
    console.log("=>" + String(contract_id))

    const defaultUnits:Unit[] = [];
    const [units, setUnits]: [Unit[], (units: Unit[]) => void] = React.useState(defaultUnits);

    //battryで一斉取得
    //->battery_id取得
    React.useEffect(() => {
    axios
        .get<Unit[]>(apiURL+"api/v1/unit/?unit_id="+unitID, {
            headers: {
                "Content-Type": "application/json"
            },
        })
        .then(response => {
            setUnits(response.data);
        });
    }, []);
    console.log('unit data should be fetched at this point');
    console.log(units)

    //customerが存在する場合は契約情報も取ってくる
    const defaultContracts:Contracts[] = []//[{contract_id:0,contract_name:"",department_id:0,contract_type:"",execution_date:0,expiration_date:0}]
    const [contracts, setContracts]: [Contracts[], (contracts: Contracts[]) => void] = React.useState(defaultContracts);
    //if (contract_id>0){
    React.useEffect(() => {
        axios
            .get<Contracts[]>(apiURL+"api/v1/contract/?contract_id="+String(contract_id), {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setContracts(response.data);
            });
        }, []);
        console.log('contract data should be fetched at this point');
        console.log(contracts)
    //}
    let ContractMap:Contracts[] = []
    if (contracts!=null && contracts.length>0){
        for (let i=0;i<contracts.length;i++){
            ContractMap.push(contracts[i])
        }
    } 

    const history = useHistory();

    return (
        <div>
        <HeaderSubBack
            headerText = "バッテリー詳細"
            onClick={() => {
                history.push({
                    pathname: `/units`
                });
            }}
        />
        <Center flexDirection="column">
            {/*
            <Box>
                <SearchBar
                    onChange={()=>{
                        //TODO:フィルタリングの処理
                    }}
                />
            </Box>
                */}
            {units.map((value) => {
                return(
                    <Box>
                        <BatteryCard2
                            remainingAmount={value.status.soc}
                            unitID={value.profile.unit_type}//製造番号（battery_id)
                            batteryType ={value.profile.purpose}//purpose
                            productID = {value.unit_id}//unit_id
                            company = {location.state.customer_name}
                            office = {location.state.department_name}
                            isCharging = {value.status.is_charging}
                            isRunning = {value.status.is_working}
                            errorMessage = {value.error.required_action}
                            />
                    </Box>
                );
            })}
            
            {units.map((value) => {
                return(
                <Box margin="15px">
                    <LongUnitDetailCard
                        //TODO:ダミーのデータのため後で入れ替え
                        unitID={value.unit_id}
                        unitType={value.profile.unit_type}
                        purpose={value.profile.purpose}
                        contractState="test"
                        BMSVersion="62B44Ce9/2019"
                        isPower={value.status.is_working}
                        message={value.error.required_action}
                        longitude={value.profile.location.longitude}
                        latitude={value.profile.location.latitude}
                        isCharging={value.status.is_charging}
                        current={value.status.current}
                        voltage={value.status.voltage}
                        errorCount={value.error.error_code.Int32}
                        soc={value.status.soc}
                        outputCurrent={value.status.output_current}
                        outputVoltage={value.status.output_voltage}
                        //isChargeError={value.error.charger_error}
                        isChargeError = {false}
                        useTime={value.status.usage_time}
                        chargeCount={value.status.number_of_charges}
                        cellMaxVol={value.status.max_cell_voltage}
                        cellMinVol={value.status.min_cell_voltage}
                        maxTemperature={value.status.max_temperature}
                        minTemperature={value.status.min_temperature}
                    />
                </Box>
                );
            })}
            <Text
                fontSize="14px"
                fontWeight="bold"
                //marginRight="100px"
            >
                所属する契約
            </Text>
            {ContractMap.map((value) => {
                    let date1:Date = new Date(String(value.execution_date))
                    let date2:Date = new Date(String(value.expiration_date))
                    return (
                        <Box margin="20px">
                            <ContractCard
                                contractName={value.contract_name}
                                contractNum={String(value.contract_id)}
                                contractType={value.contract_type}
                                startDate={formatDate(date1,DateFormat.YY_MM_DD_dd)}
                                endDate={formatDate(date2,DateFormat.YY_MM_DD_dd)}
                            />
                        </Box>
                    );
                })}

            {/*

            {CompanyDatas.map((value) => {
                return(
                    <Box margin="15px">
                        <CompanyCard
                            companyName={value.corporation_name}
                            customerID={value.customer_id}
                            onClick={() => {
                                history.push({
                                    pathname: `/customer-info`,
                                    state: {
                                        account_id: value.customer_id
                                    }
                                });
                            }}
                        />
                    </Box>
                );
            })}

            <Box marginBottom="10px">
                <BackButton
                    onClick= {() => {
                        history.goBack();
                    }}
                />
            </Box>
                */}
        </Center>
        </div>
    );
}

export default UnitDetailViewPage;