import React from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Box, Text } from '@chakra-ui/react';

import ContractUnitCard from "../../components/cards/ContractUnitCard";
import BackButton from "../../components/buttons/BackButton";
import OptionsCard from 'components/cards/OptionsCard';

interface CustomerInfoViewPageProps {
    location: {
        state: {
            customerID: string,
            contractID: string,
        }
    }
}

const CustomerFieldInfoPage = (props: CustomerInfoViewPageProps) => {
    const {location} = props
    const contractID = location.state.contractID

    const history = useHistory();

    //contractIDを元に
    const dummyContracts = {
        contractID: "0001",
        departmentID: "1234",
        customerID: "0001",
        departmentName: "A事業所",
        contractName: "テスト契約",
        contractType: "テスト契約",
        customerName: "ROCKET BATTERY",
        deadline: "2025/03/31",
    }

    //contractIDを元に,battery_option&info_type=fieldのものを取得
    const dummyCars = [
        {
            batteryOptionID: "0001",
            batteryOptionName: "車体A",
        },
        {
            batteryOptionID: "0002",
            batteryOptionName: "車体B",
        },
    ]

    const dummychargers = [
        {
            chargerOptionID: "0001",
            chargerOptionName: "充電器A",
        },
    ]

    return(
        <Center flexDirection="column">
            <Text
                w="290px"
                fontSize="22px"
                fontWeight="bold"
                margin="10px"
            >
                {dummyContracts.customerName}
            </Text>

            <ContractUnitCard 
                contractName={dummyContracts.contractName}
                contractID = {dummyContracts.contractID}
                customerName={dummyContracts.customerName}
                departmentName={dummyContracts.departmentName}
                contractType={dummyContracts.contractType}
                deadLine={dummyContracts.deadline}
            />

            <Text
                w="290px"
                fontSize="12px"
                fontWeight="bold"
                margin="10px"
            >
                各現場情報から、製造情報を登録してください。
            </Text>

            <Text
                w="290px"
                fontSize="18px"
                fontWeight="light"
                margin="5px"
            >
                現場情報
            </Text>

            <Text
                w="290px"
                fontSize="14px"
                fontWeight="light"
                margin="10px"
            >
                バッテリー製造登録
            </Text>

            {dummyCars.map((value)=>{
                return(
                    <Box marginTop="10px" marginBottom="10px">
                        <OptionsCard
                            optionName = {value.batteryOptionName}
                            optionID = {value.batteryOptionID}
                            onClick={()=>{
                                history.push({
                                    pathname: '/create-manu-battery',
                                    state: {
                                        batteryOptionID : value.batteryOptionID,
                                    }
                                });
                            }}
                        />
                    </Box>
                );
            })}

            <Text
                w="290px"
                fontSize="14px"
                fontWeight="light"
                margin="10px"
            >
                充電器製造登録
            </Text>
            
            {dummychargers.map((value)=>{
                return(
                    <Box marginTop="10px" marginBottom="10px">
                        <OptionsCard
                            optionName = {value.chargerOptionName}
                            optionID = {value.chargerOptionID}
                            onClick={()=>{
                                history.push({
                                    pathname: '/create-manu-charger',
                                    state: {
                                        chargerOptionID : value.chargerOptionID,
                                    }
                                });
                            }}
                        />
                    </Box>
                );
            })}
            <Box marginTop="10px"><BackButton
                onClick={()=>{
                    history.push({
                        pathname: '/undefined-manu-contracts',
                        state: {
                        }
                    });
                }}
            /></Box>

        </Center>
    )

}

export default CustomerFieldInfoPage