import React from "react";
import { Text, Center } from "@chakra-ui/react";

interface OfficeCardProps {
    officeName: string,
    onClick?: (value: string) => void;
}

const OfficeCard = (props: OfficeCardProps) => {
    const {officeName, onClick} = props;

    return (
        <Center
            w="267px"
            padding="10px"
            flexDirection="row"
            boxShadow="outline"
            style={{boxShadow: onClick !== undefined ?
                "2px 2px 4px rgba(0, 0, 0, 0.2)"
                : "0px 1px 0px rgba(0, 0, 0, 0.2)"
            }}
            onClick={() => {
                if (onClick !== undefined) {
                    onClick(officeName)
                }
            }}>
            <Text
                fontSize="14px"
                fontWeight="bold"
            >
                {officeName}
            </Text>
        </Center>
    );
}

export default OfficeCard;