import React from "react";
import {
    Flex,
    Text,
    Center,
} from "@chakra-ui/react";

interface UnitDetailCardProps {
    unitID: string,
    modelNum: number,
    purpose: string,
    isPower: boolean,
    useTime: number,
    chargeNum: number,
    soc: number,
    voltage: number,
    current: number,
    isError?: boolean,
    isChargeError?: boolean,
    isDeepShadow?: boolean,
}

const UnitDetailCard = (props: UnitDetailCardProps) => {
    const {
        unitID,
        modelNum,
        purpose,
        isPower,
        useTime,
        chargeNum,
        soc,
        voltage,
        current,
        isError,
        isChargeError,
        isDeepShadow,
    } = props;

    return (
        <Center
            w="267px"
            padding="20px"
            flexDirection="column"
            boxShadow="outline"
            style={{boxShadow: isDeepShadow ?
                "2px 2px 4px 6px rgba(0, 0, 0, 0.2)"
                : "2px 2px 4px rgba(0, 0, 0, 0.2)"
            }}
        >
            <Flex direction="row">
                <Text fontSize="14px" fontWeight="bold" color="gray" w="90px">ユニットID</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{unitID}</Text>
            </Flex>
            <Flex direction="row">
                <Text fontSize="14px" fontWeight="bold" color="gray" w="90px">型番</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{modelNum}</Text>
            </Flex>
            <Flex direction="row">
                <Text fontSize="14px" fontWeight="bold" color="gray" w="90px">用途</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{purpose}</Text>
            </Flex>
            <Flex direction="row">
                <Text fontSize="14px" fontWeight="bold" color="gray" w="90px">電源</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{isPower ? "入" : "切"}</Text>
            </Flex>
            <Flex direction="row">
                <Text fontSize="14px" fontWeight="bold" color="gray" w="90px">使用時間</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{`${useTime}H`}</Text>
            </Flex>
            <Flex direction="row">
                <Text fontSize="14px" fontWeight="bold" color="gray" w="90px">充電回数</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{`${chargeNum}回`}</Text>
            </Flex>
            <Flex direction="row">
                <Text fontSize="14px" fontWeight="bold" color="gray" w="90px">soc</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{`${soc}%`}</Text>
            </Flex>
            <Flex direction="row">
                <Text fontSize="14px" fontWeight="bold" color="gray" w="90px">電圧</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{`${voltage}V`}</Text>
            </Flex>
            <Flex direction="row">
                <Text fontSize="14px" fontWeight="bold" color="gray" w="90px">電流</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{`${current}A`}</Text>
            </Flex>
            <Flex direction="row">
                <Text fontSize="14px" fontWeight="bold" color="gray" w="90px">エラー</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{isError ? "顧客に連絡してください" : "なし"}</Text>
            </Flex>
            <Flex direction="row">
                <Text fontSize="14px" fontWeight="bold" color="gray" w="90px">充電器エラー</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{isChargeError ? "顧客に連絡してください" : "なし"}</Text>
            </Flex>
        </Center>
    );
}

export default UnitDetailCard;