import React from "react";
import dayjs from 'dayjs';
import 'dayjs/locale/ja'; // これimportしないとエラー吐かれる

export const apiURL = process.env.REACT_APP_API_URL

export enum DateFormat {
    YY_MM_DD_dd = 'YYYY/MM/DD(dd)',
    MM_DD_dd = 'MM/DD(dd)',
  } // enum型で表示パターンを書いておくと後で楽(jsはenum使えないけど...)
  
export function formatDate(date: Date, type: DateFormat): string {
    switch (type) {
      case DateFormat.YY_MM_DD_dd:
        return dayjs(date)
          .locale('ja')
          .format('YYYY/MM/DD'); 
      case DateFormat.MM_DD_dd:
        return dayjs(date)
          .locale('ja')
          .format('MM/DD(dd)');
      default:
        return '';
    }
  };

