import React from 'react';
import { useHistory } from 'react-router-dom';
import {
    Center,
    Text,
    Flex,
    Box,
} from '@chakra-ui/react';
import axios from 'axios';

import {apiURL,BatteryOption,BatteryOptionJson,Charger,ChargerJson,Units} from "../../modules/interfaces"
import BackButton from "../../components/buttons/BackButton";
import SubmitButton from "../../components/buttons/SubmitButton";
import CarMiniCard2 from "../../components/cards/CarMiniCard2";
import ChargerMiniCard2 from "../../components/cards/ChargerMiniCard2";
import HeaderSubBack from "../../components/HeaderSubBack"

import NameCard from "../../components/cards/NameCard";

interface BatteryRegistrationReviewPageProps {
    location: {
        state:{
            company: string,
            officeName: string,
            contractName: string,
            contract_id: number,
            account_id: number,
            department_id: number,
            battery_option_id: number [],
            charger_id: number [],
            charger_name: string [],
            option_name: string [],
            carName?: string,
            chargerName?: string,
            option_num?: number [],//battery
            charger_num?: number [],
        }
    }
}

const BatteryRegistrationReviewPage = (props: BatteryRegistrationReviewPageProps) => {
    const { location } = props;
    const company = location.state.company;
    const officeName = location.state.officeName;
    const contractName = location.state.contractName;
    const contract_id = location.state.contract_id;
    const carName = location.state.carName || "";
    const chargerName = location.state.chargerName || "";

    const account_id = location.state.account_id;
    const department_id = location.state.department_id;
    const battery_option_id = location.state.battery_option_id || [];
    const charger_id = location.state.charger_id || [];
    const charger_name = location.state.charger_name || [];
    const option_name = location.state.option_name || [];
    const option_num = location.state.option_num || [];
    const charger_num = location.state.charger_num || [];

    const history = useHistory();

    console.log("contract_is is")
    console.log(contract_id)
    console.log("battery_option_id & charger_id")
    console.log(battery_option_id)
    console.log(charger_id)

    //battery_idおよびcharger_idそれぞれのnameを持つ配列を作成
    interface name {
        this_name: string,
        this_idx: number,
        this_num: number,
    }
    let batterys_name :name [] = []
    let chargers_name :name [] = []

    if (battery_option_id.length>0){
        for (let i=0;i<battery_option_id.length;i++){
            //let tmp :name = {this_name:"車体"+String(battery_option_id[i]),this_idx:i} 
            let tmp :name = {this_name:option_name[i],this_idx:i,this_num:option_num[i]||1}
            batterys_name.push(tmp) 
        }
    }
    if(charger_id.length>0){
        for (let i=0;i<charger_id.length;i++){
            //let tmp :name = {this_name:"充電器"+String(charger_id[i]),this_idx:i}//要検討
            let tmp :name = {this_name:charger_name[i],this_idx:i,this_num:charger_num[i]||1}
            chargers_name.push(tmp)
        }
    }

    let options_datas: BatteryOption[] = [];
    
    //battery_option_id[]のそれぞれのデータをDBから取得
    for (let i=0;i<battery_option_id.length;i++){
        axios
            .get<BatteryOption[]>(apiURL+"api/v1/batteryoption/?battery_option_id=" + battery_option_id[i], {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                //setOptions(response.data);
                options_datas.push(response.data[0])
            })
        //}
    }
    console.log('battery options data should be fetched at this point.')
    console.log(options_datas)

    //battery_option 最新idを入手
    //battery_optionデータの取得
    const defaultOptions:BatteryOption[] = [];
    const [options, setOptions]: [BatteryOption[], (options: BatteryOption[]) => void] = React.useState(defaultOptions);

    React.useEffect(() => {
    axios
        .get<BatteryOption[]>(apiURL+"api/v1/batteryoptions/", {
            headers: {
                "Content-Type": "application/json"
            },
        })
        .then(response => {
            setOptions(response.data);
        })
    },[]);
    
    console.log(options)

    console.log("battery option's last_id is")
    let option_last_id = 0
    if(options.length!=0){
        let len =  options.length
        option_last_id  = options[len-1].battery_option_id
    }
    console.log(option_last_id)

    let chargers_datas: Charger[] = [];

    for (let i=0;i<charger_id.length;i++){
        axios
            .get<Charger[]>(apiURL+"api/v1/charger/?charger_id=" + charger_id[i], {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                //setOptions(response.data);
                chargers_datas.push(response.data[0])
            })
        //}
    }
    console.log('chargers data should be fetched at this point.')
    console.log(chargers_datas)

    //charger 最新idを入手
    //chargerデータの取得
    const defaultChargers:Charger[] = [];
    const [chargers, setChargers]: [Charger[], (chargers: Charger[]) => void] = React.useState(defaultChargers);
    const noremove = true;
    const noheader = true;

    React.useEffect(() => {
    axios
        .get<Charger[]>(apiURL+"api/v1/chargers/", {
            headers: {
                "Content-Type": "application/json"
            },
        })
        .then(response => {
            setChargers(response.data);
        })
    },[]);
    
    console.log(chargers)

    console.log("charger's last_id is")
    let charger_last_id = 0
    if(chargers.length!=0){
        let len =  chargers.length
        charger_last_id  = chargers[len-1].charger_id
    }
    console.log(charger_last_id)

    return (
        <div>
        <HeaderSubBack
          headerText="登録内容の確認"
          onClick={() => {
            //location.state.fieldView1Data.forkliftType=""
            history.push({
              pathname: "/battery-registration",
              state: {
                company: company,
                officeName: officeName,
                contractName: contractName,
                contract_id: contract_id,
                account_id: account_id,
                department_id: department_id,
                battery_option_id: battery_option_id,
                charger_id: charger_id,
                charger_name: charger_name,
                option_name: option_name,
                option_num: option_num,
                charger_num: charger_num,
                //carName: carNameState,
                //chargerName: chargerNameState,
            }
            });
          }}
        />
        <Center flexDirection="column">
            <Text
                w="260px"
                fontSize="16px"
                fontWeight="bold"
                margin="20px"
            >
                以下の登録内容でよろしいですか？
            </Text>
            <Flex direction="row" marginBottom="5px">
                <Text
                    w="80px"
                    fontSize="16px"
                    fontWeight="bold"
                    color="#504E4E"
                >
                    企業名
                </Text>
                <Text
                    w="150px"
                    fontSize="16px"
                    fontWeight="bold"
                >
                    {company}
                </Text>
            </Flex>
            <Flex direction="row" marginBottom="5px">
                <Text
                    w="80px"
                    fontSize="16px"
                    fontWeight="bold"
                    color="#504E4E"
                >
                    事業所
                </Text>
                <Text
                    w="150px"
                    fontSize="16px"
                    fontWeight="bold"
                >
                    {officeName}
                </Text>
            </Flex>
            <Flex direction="row" marginBottom="5px">
                <Text
                    w="80px"
                    fontSize="16px"
                    fontWeight="bold"
                    color="#504E4E"
                >
                    契約名
                </Text>
                <Text
                    w="150px"
                    fontSize="16px"
                    fontWeight="bold"
                >
                    {contractName}
                </Text>
            </Flex>
            <Flex direction="row" marginBottom="30px">
                <Text
                    w="80px"
                    fontSize="16px"
                    fontWeight="bold"
                    color="#504E4E"
                >
                    契約ID
                </Text>
                <Text
                    w="150px"
                    fontSize="16px"
                    fontWeight="bold"
                >
                    {contract_id}
                </Text>
            </Flex>
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                バッテリーを載せる車体
            </Text>
            {batterys_name.map((value) => {
                    return (
                        <Box marginBottom="20px">
                            <CarMiniCard2
                                //name={carNameState}
                                name = {value.this_name}
                                carnum = {value.this_num}
                                onRemoveHandler={() => {
                                    console.log("remove")
                                }}
                                noremove = {noremove}
                                noheader = {noheader}
                            />
                        {/* 
                            <NameCard //name={carName}/
                                name  = {"車体名：" + value.this_name}
                            />
                        */}
                        </Box>
                    );
            })}
            <Text
                w="260px"
                fontSize="11px"
                fontWeight="bold"
                color="#504E4E"
                marginBottom="10px"
            >
                製造する充電器情報
            </Text>
            {chargers_name.map((value) => {
                    return ( 
                        <Box marginBottom="20px">
                            <ChargerMiniCard2
                                //name={carNameState}
                                name = {value.this_name}
                                chargernum = {value.this_num}
                                onRemoveHandler={() => {
                                    console.log("remove")
                                }}
                                noremove = {noremove}
                                noheader = {noheader}
                            />
                            {/*
                            <NameCard //name={carName}/
                                name  = {"充電器名：" + value.this_name}
                            />
                            */}
                        </Box>
                    );
            })}
                {/*
                <BackButton
                    onClick= {() => {
                        history.goBack();
                    }}
                />
                */}
            <Box marginRight="10px" w="260px">
                <SubmitButton
                    onClick= {() => {
                        //battery_optionらをcopyして新たにDBに入れる&契約idを付与
                        console.log(options_datas)
                        let json : BatteryOptionJson;
                        for (let i=0;i<options_datas.length;i++){
                            for (let ii=0;ii<option_num[i];ii++){
                                json = {
                                    battery_option_id: String(option_last_id+i+ii+1),
                                    department_id: String(options_datas[i].department_id),
                                    contract_id: String(contract_id),
                                    update_date: String(options_datas[i].update_date.Time),
                                    info_type: options_datas[i].info_type,
                                    option_name: options_datas[i].option_name,
                                    forklift: options_datas[i].forklift,
                                    type: options_datas[i].type,
                                    serial_number: options_datas[i].serial_number,
                                    voltage: String(options_datas[i].voltage.Float64),
                                    capacity: String(options_datas[i].capacity.Float64),
                                    weight: String(options_datas[i].weight.Float64),
                                    vertical: String(options_datas[i].vertical.Float64),
                                    horizontal: String(options_datas[i].horizontal.Float64),
                                    height: String(options_datas[i].height.Float64),
                                    how2change: options_datas[i].how2change,
                                    daily_charging_minute: String(options_datas[i].daily_charging_minute.Int32),
                                    daily_working_minute: String(options_datas[i].daily_working_minute.Int32),
                                    number_of_change: String(options_datas[i].number_of_change.Int32),
                                    forklift_environment: options_datas[i].forklift_environment,
                                    environment_else: options_datas[i].environment_else,
                                    input_plug: options_datas[i].input_plug,
                                    output_plug: options_datas[i].output_plug,
                                    change_help: options_datas[i].change_help,
                                    comment: options_datas[i].comment,
                                    request: options_datas[i].request,
                                    pic_forklift: options_datas[i].pic_forklift,
                                    //pic_forklift_plate: number,
                                    //pic_battery: number,
                                    //pic_charger_place: number,
                                    //pic_battery_plug: number,
                                    //pic_forklift_plug: number,
                                    //pic_change_equipment: number,
                                }

                                axios
                                    .post(apiURL+"api/v1/batteryoption/post/", json,
                                        {
                                            headers: {
                                                "Content-Type": "applicaton/json",
                                                //"Content-Type": "text/plain",
                                            },
                                        }
                                    )
                                    .then(function (response) {
                                        console.log(response.data)
                                        })
                                    .catch(function (error) {
                                        console.log(error)
                                        })
                                    .then(function () {
                                        console.log ("*** 終了 ***")
                                        })
        
                                    console.log('add new battery options to DB');
                            }
                        }

                        console.log(chargers_datas)
                        let json2 : ChargerJson;
                        for (let i=0;i<chargers_datas.length;i++){
                            for (let ii=0;ii<charger_num[i];ii++){
                                json2 = {
                                    charger_id: String(charger_last_id+i+ii+1),
                                    department_id: String(chargers_datas[i].department_id),
                                    contract_id: String(contract_id),
                                    update_date: String(chargers_datas[i].update_date.Time),
                                    info_type: chargers_datas[i].info_type,
                                    charger_name: chargers_datas[i].charger_name,
                                    charger_environment: chargers_datas[i].charger_environment,
                                    charger_environment_else: chargers_datas[i].charger_environment_else,
                                    how2supply: chargers_datas[i].how2supply,
                                    supply_plug: chargers_datas[i].supply_plug,
                                    supply_else: chargers_datas[i].supply_else,
                                    power_supply_ampere: String(chargers_datas[i].power_supply_ampere.Int32),
                                    stand: chargers_datas[i].stand,
                                    power_supply2charger_cable_length: String(chargers_datas[i].power_supply2charger_cable_length.Float64),
                                    charger2forklift_cable_length: String(chargers_datas[i].charger2forklift_cable_length.Float64),
                                    charger_setting_help: chargers_datas[i].charger_setting_help,
                                    comment: chargers_datas[i].comment,
                                    pic_charger_stand: chargers_datas[i].pic_charger_stand,
                                }

                                axios
                                    .post(apiURL+"api/v1/charger/post/", json2,
                                        {
                                            headers: {
                                                "Content-Type": "applicaton/json",
                                                //"Content-Type": "text/plain",
                                            },
                                        }
                                    )
                                    .then(function (response) {
                                        console.log(response.data)
                                        })
                                    .catch(function (error) {
                                        console.log(error)
                                        })
                                    .then(function () {
                                        console.log ("*** 終了 ***")
                                        })
        
                                    console.log('add new chargers to DB');
                            }
                        }
                        history.push({
                            pathname: `/contract-complete2`,
                            state: {
                                company: company,
                                officeName: officeName,
                                contractName: contractName,
                                contract_id: contract_id,
                                account_id: account_id,
                                department_id: department_id,
                                battery_option_id: battery_option_id,
                                charger_id: charger_id,
                                charger_name: charger_name,
                                option_name: option_name,
                                //carName: carName,
                                //chargerName: chargerName,
                            }
                        })
                    }}
                />
                </Box>
        </Center>
        </div>
    );
}

export default BatteryRegistrationReviewPage;