import React from "react";
import { useHistory } from 'react-router-dom';
import {Center, Box, Text} from "@chakra-ui/react";

import {useWindowDimensions} from "../../utils/WindowDimensions";
import BackButton from "../../components/buttons/BackButton";

interface CustomerChargerOptionPageProps {
    location: {
        state: {
            customerID: string,
            chargerOptionID: string,
            departmentID: string,
        }
    }
}

const CustomerChargerOptionPage = (props: CustomerChargerOptionPageProps) => {
    const {location} = props
    let {width, height} = useWindowDimensions();
    const chargerOptionID = location.state.chargerOptionID
    const departmentID = location.state.departmentID
    const customerID = location.state.customerID

    const history = useHistory();

    //chargerOptionIDを元にゲトる
    const dummyChargerOptionData = {
        chargerOptionName: "充電器A",       
    }

    return(
        <Center w={width} flexDirection="column">
            <Text
                fontSize="22px"
                fontWeight="bold"
                margin="10px"
            >
                現場詳細情報
            </Text>
            <Text>
                {dummyChargerOptionData.chargerOptionName}
            </Text>

            <BackButton
                onClick={()=>{
                    history.push({
                        pathname: '/customer-department',
                        state: {
                            customerID: customerID,
                            departmentID: departmentID,
                        }
                    });
                }}
            />
        </Center>
    )
}

export default CustomerChargerOptionPage