import React from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Box, Text, Divider } from '@chakra-ui/react';
import axios from 'axios';

import {apiURL,Units,Customers,Contracts,Departments} from "../../modules/interfaces"

import Header from "../../components/Header";
import SearchBar from "../../components/SearchBar";
import AddCustomerButton from "../../components/buttons/AddButton";
import CompanyCard from "../../components/cards/CompanyCard";
import CustomerCard from 'components/components2/CustomerCard';

const CustomerViewPage = () => {

    const history = useHistory();

    //企業
    const defaultCustomers:Customers[] = [];
    const [customers, setCustomers]: [Customers[], (customers: Customers[]) => void] = React.useState(defaultCustomers);
    React.useEffect(() => {
        axios
            .get<Customers[]>(apiURL+"api/v1/customers/", {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setCustomers(response.data);
            });
        }, []);
        console.log('customers data should be fetched at this point');
        console.log(customers)

    const defaultDepartments:Departments[] = [{department_id:0,department_name:"",parent_id:0,postal_code:"",address:"",daily_working_hour:{Int32:0,Valid:false},weekly_holiday:{Int32:0,Valid:false},name:"",position:"",mail:"",phone:""}]
    const [departments, setDepartments]: [Departments[], (departments: Departments[]) => void] = React.useState(defaultDepartments);
    React.useEffect(() => {
        axios
            //.get<Departments[]>(apiURL +"api/v1/customerdepartment/?parent_id="+String(customer_id), {
            .get<Departments[]>(apiURL +"api/v1/departments/", {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setDepartments(response.data);
            });
        }, []);
        console.log('departments data should be fetched at this point');
        console.log(departments)
    
    let department_ids = []
    if(customers!=null && customers.length>0 && departments!=null && departments.length>0){
        for(let i=0;i<customers.length;i++){
            let tmp = []
            for(let ii=0;ii<departments.length;ii++){
                if(customers[i].account_id==departments[ii].parent_id){
                    tmp.push(departments[ii].department_id)
                }
            }
            if(tmp.length==0){
                tmp=[-1]
            }
            department_ids.push(tmp)//[[1,2],[3],[...]]
        }
    }
    //console.log("?")
    //console.log(departments)
    //console.log(department_ids)
    
    const defaultContracts:Contracts[] = [{contract_id:0,contract_name:"",department_id:0,contract_type:"",execution_date:0,expiration_date:0}]
    const [contracts, setContracts]: [Contracts[], (contracts: Contracts[]) => void] = React.useState(defaultContracts);
    React.useEffect(() => {
        axios
            .get<Contracts[]>(apiURL+"api/v1/contracts/", {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setContracts(response.data);
            });
        }, []);
        console.log('contract data should be fetched at this point');
        console.log(contracts)
    
    
    let contract_num:number[]=[]
    //let contract_list:number[][]=[]
    if(customers!=null && customers.length>0 && contracts!=null){
        for(let i=0;i<customers.length;i++){
            let tmp=0
            //let tmp2:number[] =[]
            if(department_ids[i]!=null){
                for(let ii=0;ii<department_ids[i].length;ii++){
                    for(let iii=0;iii<contracts.length;iii++){
                        if(department_ids[i][ii]==contracts[iii].department_id){
                            tmp+=1
                            //tmp2.push(contracts[iii].contract_id)
                        }
                    }
                }
            }
            //contract_list.push(tmp2)
            contract_num.push(tmp)
        }
    }
    //console.log("??")
    //console.log(contract_num)

    return (
        <div>
        <Header
          drawerItems={[
            {
              title: "ホーム",
              path: "/",
            },
            {
              title: "バッテリー",
              path: "/units",
            },
            {
              title: "お客様",
              path: "/customer-view",
            },
          ]}
          //iconName={iconname}
        />
        <Center flexDirection="column">
            <Text
                w="290px"
                fontSize="22px"
                fontWeight="bold"
                margin="10px"
            >
                お客様一覧
            </Text>
            {/*
            <Box>
                <Text fontSize="14px" margin="10px">新規契約登録：お客様詳細ページから、事業所を選択</Text>
            </Box>
            <Divider/>
            */}
            {/*
            <Box>
                <SearchBar
                    onChange={() => {
                        //TODO:フィルタリングの処理を入れる
                    }}
                />
            </Box>
            <Box margin="30px">
                <AddCustomerButton
                    label="顧客を追加"
                    onClick={() => {
                        history.push(`/registration-view`);
                    }}
                />
            </Box>
            */}
            {customers.map((value,index) => {
                return (
                    <Box marginTop="30px">
                        <CustomerCard
                            customerName={value.corporation_name}
                            customerID={"RB_CUS_"+String(value.account_id)}
                            industry = {value.sector}
                            contractNum = {contract_num[index]}
                            onClick={() => {
                                history.push({
                                    //pathname: "/customer-units",
                                    pathname: "/customer-info",
                                    state: {
                                        account_id: value.account_id,
                                        //contract_list: contract_list[index]
                                    }
                                });
                            }}
                        />
                        {/*
                        <CompanyCard
                            customerID={String(value.account_id)}
                            companyName={value.corporation_name}
                            jobType={value.sector}
                            onClick={() => {
                                history.push({
                                    //pathname: "/customer-units",
                                    pathname: "/customer-info",
                                    state: {
                                        account_id: value.account_id,
                                    }
                                });
                            }}
                        />
                        */}
                    </Box>
                );
            })}
        </Center>
        </div>
    );
}

export default CustomerViewPage;