import React from "react";
import {
    Flex,
    Text,
    Center,
    Image,
} from "@chakra-ui/react";
import rocket_icon from "../../assets/rocket_icon.png";

interface ContractCompleteCardProps {
    companyName: string,
    contractID: string,
    contractName: string,
    iconURL?: string,
    onClick?: (value: string) => void,
}

const ContractCompleteCard = (props: ContractCompleteCardProps) => {
    const {
        companyName,
        contractID,
        contractName,
        iconURL,
        onClick,
    } = props;

    return (
        <Center
            w="267px"
            padding="10px"
            flexDirection="row"
            boxShadow="outline"
            style={{boxShadow: onClick !== undefined ?
                "2px 2px 4px rgba(0, 0, 0, 0.2)"
                : "0px 1px 0px rgba(0, 0, 0, 0.2)"
            }}
            onClick={() => {
                if (onClick !== undefined) {
                    onClick(contractID)
                }
            }}>
            <Image w="63px" src={iconURL || rocket_icon}/>
            <Flex direction="column" marginLeft="20px">
                <Text fontSize="14px" fontWeight="bold">{companyName}</Text>
                <Flex>
                    <Text fontSize="14px" fontWeight="bold" color="gray" w="50px">契約名</Text>
                    <Text fontSize="14px" fontWeight="bold" w="100px">{contractName}</Text>
                </Flex>
                <Flex>
                    <Text fontSize="14px" fontWeight="bold" color="gray" w="50px">契約ID</Text>
                    <Text fontSize="14px" fontWeight="bold" w="100px">{contractID || ""}</Text>
                </Flex>
            </Flex>
        </Center>
    );
}

export default ContractCompleteCard;