import React, {useState} from "react";
import {
    Flex,
    Text,
    Center,
} from "@chakra-ui/react";
import {ThemeColor} from "../../utils/ThemeColor"

interface OptionsCardProps {
    optionName: string,
    optionID: string,
    onClick?: (value: string) => void,
}

const OptionsCard = (props: OptionsCardProps) => {
    const {
        optionName,
        optionID,
        onClick
    } = props;

    return (
        <Center
            w="267px"
            //padding="20px"
            flexDirection="column"
            boxShadow="outline"
            style={{boxShadow: onClick !== undefined ?
                "2px 2px 4px rgba(0, 0, 0, 0.2)"
                : "0px 1px 0px rgba(0, 0, 0, 0.2)"
            }}
            onClick={() => {
                if (onClick !== undefined) {
                    onClick(optionID)
                }
            }}>
            <Text fontSize="18px" fontWeight="bold" margin="10px">{optionName}</Text>
        </Center>
    )
}

export default OptionsCard;