import { Flex, Text, Spacer, Icon, Center } from "@chakra-ui/react";
import { MdError } from "react-icons/md";

interface PlantCardProps {
  officeName: string;
  plantID: string;
  companyName: string;
  postalCode: string;
  address: string;
  errorMessage?: string;
  onClick?: (value: string) => void;
}

const PlantCard = (props: PlantCardProps) => {
  const {
    officeName,
    plantID,
    companyName,
    postalCode,
    address,
    errorMessage,
    onClick,
  } = props;

  return (
    <Center
      w="267px"
      h="170"
      color="#504E4E"
      flexDirection="column"
      boxShadow="outline"
      style={{
        boxShadow: "0px 4px 5px rgba(0, 0, 0, 0.5)",
      }}
      onClick={() => {
        if (onClick !== undefined) {
          onClick(plantID);
        }
      }}
    >
      <Flex
        w="200px"
        h="22px"
        color="#EB5757"
        fontSize="10px"
        direction="row"
        border="1px"
        borderColor="#EB5757"
        borderRadius="3px"
        alignItems="center"
        padding="5px"
        visibility={errorMessage ? "visible" : "hidden"}
      >
        <Icon w="18px" h="18px" as={MdError} />
        <Spacer />
        <Text>{errorMessage}</Text>
        <Spacer />
      </Flex>
      <Text margin="5px" fontSize="18px" fontWeight="bold">
        {officeName}
      </Text>
      <Text marginBottom="5px" fontSize="12px">
        {plantID}
      </Text>
      <Text fontSize="14px" marginBottom="5px">
        {companyName}
      </Text>
      <Flex w="180px" direction="column">
        <Text fontWeight="light" fontSize="10px">{`〒${postalCode}`}</Text>
        <Text fontWeight="light" fontSize="12px">
          {address}
        </Text>
      </Flex>
    </Center>
  );
};

export default PlantCard;
