import React from 'react';
import { useHistory } from 'react-router-dom';
import { Center, Box, Text } from '@chakra-ui/react';
import axios from 'axios';

import {apiURL,Units,Customers} from "../../modules/interfaces"

import BatteryCard from "../../components/cards/BatteryCard";
import SearchBar from "../../components/SearchBar";

interface CustomerUnitsViewPageProps {
    location: {
        state: {
            account_id: number,
        }
    }
}

const CustomerUnitsViewPage = (props: CustomerUnitsViewPageProps) => {
    const { location } = props;
    const account_id = location.state.account_id;
    console.log("account_id: " + account_id)

    const defaultUnits:Units[] = [];
    const [units, setUnits]: [Units[], (units: Units[]) => void] = React.useState(defaultUnits);

    //battryで一斉取得
    //->battery_id取得
    React.useEffect(() => {
    axios
        .get<Units[]>(apiURL+"api/v1/units/", {
            headers: {
                "Content-Type": "application/json"
            },
        })
        .then(response => {
            setUnits(response.data);
        });
    }, []);
    console.log('units data should be fetched at this point');
    console.log(units)

    //account_idを元にcustomer_nameを取得
    const defaultCustomers:Customers[] = [];
    const [customers, setCustomers]: [Customers[], (customers: Customers[]) => void] = React.useState(defaultCustomers);

    React.useEffect(() => {
        axios
            .get<Customers[]>(apiURL+"api/v1/customers/", {
                headers: {
                    "Content-Type": "application/json"
                },
            })
            .then(response => {
                setCustomers(response.data);
            });
        }, []);
        console.log('customers data should be fetched at this point');
        console.log(customers)

    let customer_units: Units[] = []
    let len=0
    let len2=0
    if(units.length!=0){
        len = units.length
        len2 = customers.length
        
        if(customers.length!=0){
            let customer_idx=-1;
            for (let i=0;i<len2;i++){
                if(customers[i].account_id==account_id){
                    customer_idx=i
                }
            }
            for (let i=0;i<len;i++){
                if(customer_idx>=0 && units[i].customer_name==customers[customer_idx].corporation_name){
                    customer_units[i]=units[i]
                }
            }
        }
    }

    console.log(customer_units)

    const history = useHistory();

    return (
        <Center flexDirection="column">
            <Text
                w="290px"
                fontSize="22px"
                fontWeight="bold"
                margin="10px"
            >
                バッテリー一覧
            </Text>
            <Box>
                <SearchBar
                    onChange={()=>{
                        //TODO:フィルタリングの処理
                    }}
                />
            </Box>
            {customer_units.map((value) => {
                return (
                    <Box margin="15px">
                        <BatteryCard
                            remainingAmount={value.soc}
                            unitID={value.unit_id}
                            batteryType={value.profile.unit_type}
                            company={value.customer_name}
                            purpose={value.profile.purpose}
                            requiredAction={value.required_action}
                            onClick={() => {
                                history.push({
                                    pathname: `/unit-detail`,
                                    state: {
                                        contractID: value.contract_id,
                                        unitID: value.unit_id
                                        /*
                                        unitData: {
                                            remainingAmount: value.soc,
                                            unitID: value.unit_id,
                                            batteryType: value.profile.unit_type,
                                            company: value.customer_name,
                                            purpose: value.profile.purpose,
                                        }
                                        */
                                    }
                                });
                            }}
                        />
                    </Box>
                );
            })}
        </Center>
    );
}

export default CustomerUnitsViewPage;