import React, {useState} from "react";
import {
    Flex,
    Text,
    Center,
} from "@chakra-ui/react";
import {ThemeColor} from "../../utils/ThemeColor"

interface ContractUnitCardProps {
    contractName: string,
    customerName: string,
    departmentName: string,
    contractID: string,
    contractType: string,
    deadLine: string,
    onClick?: (value: string) => void,
}

const ContractUnitCard = (props: ContractUnitCardProps) => {
    const {
        contractName,
        contractID,
        customerName,
        departmentName,
        contractType,
        deadLine,
        onClick
    } = props;

    return (
        <Center
            w="267px"
            padding="20px"
            flexDirection="column"
            boxShadow="outline"
            style={{boxShadow: onClick !== undefined ?
                "2px 2px 4px rgba(0, 0, 0, 0.2)"
                : "0px 1px 0px rgba(0, 0, 0, 0.2)"
            }}
            onClick={() => {
                if (onClick !== undefined) {
                    onClick(contractID)
                }
            }}>
            <Text fontSize="18px" fontWeight="bold" marginBottom="15px">{contractName}</Text>
            <Flex>
                <Text fontSize="14px" fontWeight="bold" color="gray" w="80px">契約ID</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{contractID}</Text>
            </Flex>
            <Flex>
                <Text fontSize="14px" fontWeight="bold" color="gray" w="80px">顧客</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{customerName}</Text>
            </Flex>
            <Flex>
                <Text fontSize="14px" fontWeight="bold" color="gray" w="80px">事業所</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{departmentName}</Text>
            </Flex>
            <Flex>
                <Text fontSize="14px" fontWeight="bold" color="gray" w="80px">契約タイプ</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{contractType}</Text>
            </Flex>
            <Flex>
                <Text fontSize="14px" fontWeight="bold" color="gray" w="80px">契約終了日</Text>
                <Text fontSize="14px" fontWeight="bold" marginLeft="10px" w="150px">{deadLine}</Text>
            </Flex>
        </Center>
    );
}

export default ContractUnitCard;