import React from "react";
import { Flex, Text, Center, Image } from "@chakra-ui/react";
import battery_ful from "../../assets/battery-full.png";
import battery_80 from "../../assets/battery-80.png";
import battery_30 from "../../assets/battery-30.png";
import battery_20 from "../../assets/battery-20.png";

interface BatteryCardProps {
  remainingAmount: number;
  unitID: string;
  batteryType: string;
  company: string;
  purpose: string;
  requiredAction?: string;
  onClick?: (value: string) => void;
}

const BatteryCard = (props: BatteryCardProps) => {
    const {remainingAmount, unitID, batteryType, company, purpose, requiredAction, onClick} = props;

    const getBatteryLogo = () => {
        if (remainingAmount === 100) {
            return battery_ful;
        } else if (remainingAmount >= 80) {
            return battery_80;
        } else if (remainingAmount >= 30) {
            return battery_30;
        } else if (remainingAmount >= 20) {
            return battery_20;
        } else {
            return battery_20;
        }
    };

  let ErrorCnt: string[];
  ErrorCnt = [];
  if (props.requiredAction != "") {
    ErrorCnt[0] = "Error";
  }

  const batteryLogo = getBatteryLogo();
    return (
        <Center
            w="267px"
            padding="10px"
            flexDirection="column"
            boxShadow="outline"
            style={{boxShadow: onClick !== undefined ?
                "2px 2px 4px rgba(0, 0, 0, 0.2)"
                : "0px 1px 0px rgba(0, 0, 0, 0.2)"
            }}
            onClick={() => {
                if (onClick !== undefined) {
                    onClick(unitID);
                }
            }}>
            <Center flexDirection="row">
                <Flex direction="column">
                    <Image
                        w="55px"
                        h="50px"
                        src={batteryLogo}
                    />
                    <Center alignItems="baseline">
                        <Text fontSize="24px" fontWeight="bold">{remainingAmount}</Text>
                        <Text fontSize="14px">%</Text>
                    </Center>
                </Flex>
                <Flex w="155px" direction="column" marginLeft="20px">
                    <Text fontSize="14px" fontWeight="bold">bms ID</Text><Text marginLeft="20px" fontSize="14px" fontWeight="right">{unitID}</Text>
                    <Text fontSize="14px" fontWeight="bold">型式</Text><Text marginLeft="20px" fontSize="14px" fontWeight="right">{batteryType}</Text>
                    <Text fontSize="14px" fontWeight="bold">顧客</Text><Text marginLeft="20px" fontSize="14px" fontWeight="right">{company}</Text>
                    {/*<Text fontSize="14px" fontWeight="bold">{purpose}</Text>*/}

                    {ErrorCnt.map((value) => {
                      return (
                        <Text
                          w="61px"
                          textAlign="center"
                          fontSize="8px"
                          fontWeight="bold"
                          color="#BB2C2C"
                          border="1px"
                          borderColor="#BB2C2C"
                          borderRadius="4px"
                        >
                          ERROR
                        </Text>
                      );
                    })}
        </Flex>
      </Center>
      {requiredAction && (
        <Text fontSize="14px" fontWeight="bold" color="#BB2C2C">
          {requiredAction}
        </Text>
      )}
    </Center>
  );
};

export default BatteryCard;
