import { Flex, Text, Spacer, Icon, Center } from "@chakra-ui/react";
import { MdPower } from "react-icons/md";

interface ChargerFieldCardProps {
  chargerName: string;
  provideMethod: string;
  amps: number;
  onClick?: (chargerName: string) => void;
}

const ChargerFieldCard = (props: ChargerFieldCardProps) => {
  const { chargerName, provideMethod, amps, onClick } = props;

  return (
    <Center
      w="267px"
      h="116px"
      flexDirection="column"
      boxShadow="outline"
      style={{
        boxShadow: "0px 4px 5px rgba(0, 0, 0, 0.5)",
      }}
      onClick={() => {
        if (onClick !== undefined) {
          onClick(chargerName);
        }
      }}
    >
      <Flex direction="row" alignItems="center">
        <Icon w="30px" h="30px" as={MdPower} />
        <Text fontSize="18px" fontWeight="bold">
          {chargerName}
        </Text>
      </Flex>
      <Flex w="225px" direction="column" fontSize="12px" marginTop="10px">
        <Flex direction="row">
          <Text>電源供給方法：</Text>
          <Text fontWeight="bold">{provideMethod}</Text>
        </Flex>
        <Flex direction="row">
          <Text>電源アンペア数：</Text>
          <Text fontWeight="bold">{`${amps}A`}</Text>
        </Flex>
      </Flex>
    </Center>
  );
};

export default ChargerFieldCard;
