import { Flex, Text, Spacer, Icon, Center } from "@chakra-ui/react";
import {
  MdBatteryFull,
  MdBattery90,
  MdBattery80,
  MdBattery60,
  MdBattery50,
  MdBattery30,
  MdBattery20,
  MdBatteryChargingFull,
  MdBatteryCharging90,
  MdBatteryCharging80,
  MdBatteryCharging60,
  MdBatteryCharging50,
  MdBatteryCharging30,
  MdBatteryCharging20,
} from "react-icons/md";

interface BatteryCardProps {
  remainingAmount: number;
  unitID: string;
  batteryType: string;
  productID: string;
  company: string;
  office: string;
  isRunning?: boolean;
  isCharging?: boolean;
  errorMessage?: string;
  onClick?: (value: string) => void;
}

const getIcon = (isCharging: boolean | undefined, remainingAmount: number) => {
  if (isCharging) {
    if (remainingAmount === 100)
      return { src: MdBatteryChargingFull, color: "#62BB2C" };
    else if (remainingAmount > 80)
      return { src: MdBatteryCharging90, color: "#62BB2C" };
    else if (remainingAmount > 60)
      return { src: MdBatteryCharging80, color: "#62BB2C" };
    else if (remainingAmount > 50)
      return { src: MdBatteryCharging60, color: "#62BB2C" };
    else if (remainingAmount > 30)
      return { src: MdBatteryCharging50, color: "#FFEE32" };
    else if (remainingAmount > 20)
      return { src: MdBatteryCharging30, color: "#FFEE32" };
    else return { src: MdBatteryCharging20, color: "#FF3333" };
  } else {
    if (remainingAmount === 100)
      return { src: MdBatteryFull, color: "#62BB2C" };
    else if (remainingAmount > 80)
      return { src: MdBattery90, color: "#62BB2C" };
    else if (remainingAmount > 60)
      return { src: MdBattery80, color: "#62BB2C" };
    else if (remainingAmount > 50)
      return { src: MdBattery60, color: "#62BB2C" };
    else if (remainingAmount > 30)
      return { src: MdBattery50, color: "#FFEE32" };
    else if (remainingAmount > 20)
      return { src: MdBattery30, color: "#FFEE32" };
    else return { src: MdBattery20, color: "#FF3333" };
  }
};

const BatteryCard = (props: BatteryCardProps) => {
  const {
    remainingAmount,
    unitID,
    batteryType,
    productID,
    company,
    office,
    isRunning,
    isCharging,
    errorMessage,
    onClick,
  } = props;

  const iconData = getIcon(isCharging, remainingAmount);

  return (
    <Center
      w="267px"
      h="151"
      color="#504E4E"
      flexDirection="column"
      boxShadow="outline"
      style={{
        boxShadow:
          onClick !== undefined
            ? "0px 4px 5px rgba(0, 0, 0, 0.5)"
            : "0px 1px 0px rgba(0, 0, 0, 0.2)",
      }}
      onClick={() => {
        if (onClick !== undefined) {
          onClick(unitID);
        }
      }}
    >
      <Flex
        w="243px"
        h="20px"
        color="#BB2C2C"
        fontSize="12px"
        fontWeight="bold"
        direction="row"
        border="1px"
        borderColor="#BB2C2C"
        borderRadius="3px"
        alignItems="center"
        padding="5px"
        visibility={errorMessage ? "visible" : "hidden"}
      >
        <Text>ERROR!</Text>
        <Spacer />
        <Text>{errorMessage}</Text>
        <Spacer />
      </Flex>
      <Flex direction="row">
        <Flex direction="column">
          <Center marginTop="-15px" flexDirection="column">
            <Icon
              w="70px"
              h="100px"
              transform="rotate(90deg)"
              as={iconData.src}
              color={iconData.color}
            />
            <Flex marginTop="-35px" direction="row" alignItems="baseline">
              <Text fontWeight="bold" fontSize="24px">
                {remainingAmount}
              </Text>
              <Text fontWeight="bold" fontSize="14px">
                %
              </Text>
            </Flex>
            <Text
              w="35px"
              h="20px"
              fontSize="8px"
              fontWeight="bold"
              color={isRunning ? "#62BB2C" : "#919191"}
              border="1px"
              borderColor={isRunning ? "#62BB2C" : "#919191"}
              borderRadius="3px"
              textAlign="center"
              marginTop="3px"
            >
              {isRunning ? "on" : "off"}
            </Text>
          </Center>
        </Flex>
        <Flex w="140px" marginTop="10px" marginLeft="20px" direction="column">
          <Text fontSize="18px" fontWeight="bold">
            {unitID}
          </Text>
          <Text fontSize="12px">{batteryType}</Text>
          <Text fontSize="9px" fontWeight="light">
            {productID}
          </Text>
          <Text fontSize="12px" fontWeight="bold" marginTop="5px">
            {company}
          </Text>
          <Text fontSize="12px" fontWeight="bold">
            {office}
          </Text>
        </Flex>
      </Flex>
    </Center>
  );
};

export default BatteryCard;
